
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import {
  RCard, RCardActions, RCardSection, RatingScore,
} from '@/ui/molecules';
import { DefaultCard } from '@/ui/organisms';

@Options({
  components: {
    DefaultCard,
    RCard,
    RCardActions,
    RCardSection,
    RatingScore,
  },
})
export default class ReviewCard extends Vue {
  @Prop(String) name!: string;

  @Prop(String) comment!: string;

  @Prop(String) date!: string;

  @Prop(Number) score!: number;

  @Prop({ default: '100%' }) readonly width!: string;
}
