import { AxiosError, AxiosRequestConfig, AxiosResponse } from 'axios';
import { BaseService, CrudService } from '@/api';

import { ErrorResponse } from '@/types/models';
import {
  SmsConfirmRequest, SmsConfirmResponse, SmsLoginRequest, SmsLoginResponse,
} from './types';
import { ErrorCancel } from '@/types/common';

export class AuthorizationService<T, D> extends CrudService<T, D> {
  constructor() {
    super('/Auth');
  }

  public async sendSms(
    data: SmsLoginRequest,
    config?: AxiosRequestConfig<SmsLoginRequest>
  ): Promise<AxiosResponse<SmsLoginResponse> | AxiosError<ErrorResponse> | ErrorCancel> {
    try {
      return await BaseService.axios.post(`${this.path}/sms`, data, config);
    } catch (error: any) {
      return error;
    }
  }

  public async confirmSms(data: SmsConfirmRequest, config?: AxiosRequestConfig<SmsConfirmRequest>): Promise<SmsConfirmResponse> {
    const response = await BaseService.axios.post(`${this.path}/smsConfirm`, data, config);

    return response.data;
  }
}

export default new AuthorizationService<any, any>();
