import config from '@/config';

export function getFileUrl(fileId: string, controller = 'Files'): string {
  return `${config.BASE_URL}/${controller}/files/${fileId}`;
}

export async function getBase64(file: Blob): Promise<string> {
  return new Promise((resolve: (value: string) => void, reject: (reason: any) => void) => {
    const reader = new FileReader();

    reader.readAsDataURL(file);
    reader.onload = (): void => resolve(reader.result as string);
    reader.onerror = (): void => reject(new Error('Ошибка конвертации файла'));
  });
}

export function invokeFileDownloadFromBlob(blob: Blob | null | undefined, fileNameAndExtension: string | null | undefined): void {
  if (!blob) {
    return;
  }

  const href = URL.createObjectURL(blob);

  const link = document.createElement('a');

  link.download = fileNameAndExtension ?? 'unknown';
  link.href = href;
  link.click();

  URL.revokeObjectURL(link.href);
}
