import { App } from 'vue';

import {
  getExperienceRangeDates,
} from '@/utils';

export const uiUtils = {
  getExperienceRangeDates,
};

export function initUiUtils(app: App<Element>): App<Element> {
  const clonedApp = { ...app };

  clonedApp.config.globalProperties.$uiUtils = uiUtils;

  return clonedApp;
}
