import { Module } from 'vuex-simple';

import DynamicStore from './dynamicStore';

import SpecialitiesStore from './stores/specialities';
import AuthorizationStore from './stores/authorization';
import AuthenticationStore from './stores/authentication';
import OrdersStore from './stores/orders';
import UserStore from './stores/user';
import DoctorAppointmentStore from './stores/doctorAppointment';
import ConsultationInfoStore from './stores/consultationInfo';
import ConsultationStore from './stores/consultation/consultation';
import FilesStore from './stores/files';
import UserInfoStore from './stores/userInfo';
import ConfirmationInfoStore from './stores/confirmationInfo';
import ModalsStore from './stores/modals';
import DoctorExperienceStore from './stores/doctorExperience';

class RootStore extends DynamicStore {
  @Module()
  public specialities = new SpecialitiesStore(this);

  @Module()
  public authorization = new AuthorizationStore(this);

  @Module()
  public authentication = new AuthenticationStore(this);

  @Module()
  public orders = new OrdersStore();

  @Module()
  public doctorAppointment = new DoctorAppointmentStore();

  @Module()
  public user = new UserStore();

  @Module()
  public consultationInfo = new ConsultationInfoStore();

  @Module()
  public consultation = new ConsultationStore(this);

  @Module()
  public files = new FilesStore();

  @Module()
  public userInfo = new UserInfoStore();

  @Module()
  public confirmationInfo = new ConfirmationInfoStore(this);

  @Module()
  public modals = new ModalsStore();

  @Module()
  public doctorExperience = new DoctorExperienceStore();
}

export default RootStore;
