import { App } from 'vue';

import {
  getThemeFromSearch,
  setThemeToBrowserStorage,
  getThemeFromStorageOrDefault,
} from '@/utils';
import { defaultTheme } from '@/common';

export const theming = {
  getThemeFromSearch,
  getThemeFromStorageOrDefault,
};

function setThemeAttr(): void {
  const rootElement = document.querySelector('html');

  if (rootElement) {
    rootElement.dataset.theme = getThemeFromSearch() ?? defaultTheme;
  }
}

export function initTheming(app: App<Element>): App<Element> {
  const clonedApp = { ...app };

  clonedApp.config.globalProperties.$theming = theming;

  setThemeAttr();
  setThemeToBrowserStorage(getThemeFromSearch() ?? defaultTheme);

  return clonedApp;
}
