/* eslint-disable @typescript-eslint/explicit-function-return-type */
import { ref } from 'vue';

export function useSwitcher<T = string>(defaultSwitch: T | undefined) {
  const activeSwitch = ref<T | undefined>(defaultSwitch);

  return {
    activeSwitch,
    setActiveSwitch: (newSwitch: T | undefined): void => {
      (activeSwitch as any).value = newSwitch;
    },
    isActive: (thisSwitch: T | undefined) => (
      thisSwitch === activeSwitch.value
    ),
  };
}
