
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { ToggleBadgeProps } from './types';

import { RBadge } from '@/ui/atoms';

@Options({
  components: {
    RBadge,
  },
})
export default class ToggleBadge extends Vue implements ToggleBadgeProps {
  @Prop({ default: '' }) readonly color!: string;

  @Prop({ type: Boolean, default: false }) readonly disabled!: boolean;

  @Prop({ type: Boolean, default: false }) readonly active!: boolean;
}
