import { computed } from 'vue';

import { useSimpleStore } from '@/utils/vuex';
import { AuthenticationComposition } from './types';

export function useAuthentication(): AuthenticationComposition {
  const store = useSimpleStore();

  const decodedToken = computed(() => store.authentication.decodedToken);
  const isAuthenticated = computed(() => store.authentication.isAuthenticated);
  const isTokenRecentlyUpdated = computed(() => store.authentication.isTokenRecentlyUpdated);

  return {
    authentication: store.authentication.state,
    decodedToken,
    isAuthenticated,
    isTokenRecentlyUpdated,
  };
}
