import { computed } from 'vue';

import { useSimpleStore } from '@/utils/vuex';
import { ConsultationStatusComposition } from './types';
import { consultationStatusesClasses, consultationStatusesIcons } from '@/common';

export function useConsultationStatus(): ConsultationStatusComposition {
  const store = useSimpleStore();

  const mappedStatus = computed(() => store.consultation.mappedStatus);
  const statusClass = computed(() => consultationStatusesClasses[mappedStatus.value]);
  const statusIconName = computed(() => consultationStatusesIcons[mappedStatus.value]);

  return {
    statusClass,
    mappedStatus,
    statusIconName,
  };
}
