
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { RTabProps } from './types';

@Options({})
export default class RTab extends Vue implements RTabProps {
  @Prop(Boolean) readonly disable!: boolean;

  @Prop(String) readonly label!: string;

  @Prop(String) readonly name!: string;

  @Prop({ type: Boolean, default: true }) readonly noCaps!: boolean;

  @Prop({ type: Boolean, default: false }) readonly ripple!: boolean;
}
