import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios';

import config from '@/config';

export class BaseService<R, D = any> {
  public static axios: AxiosInstance = axios.create({
    baseURL: config.BASE_URL,
  });

  protected path: string;

  constructor(path: string) {
    this.path = path;
  }

  protected async get(config?: AxiosRequestConfig<D>): Promise<AxiosResponse<R>> {
    return BaseService.axios.get<R, AxiosResponse<R>, D>(this.path, config);
  }

  protected async post(data: D, config?: AxiosRequestConfig<D>): Promise<AxiosResponse<R>> {
    return BaseService.axios.post<R, AxiosResponse<R>, D>(this.path, data, config);
  }

  protected async patch(data: D, config?: AxiosRequestConfig<D>): Promise<AxiosResponse<R>> {
    return BaseService.axios.patch<R, AxiosResponse<R>, D>(this.path, data, config);
  }

  protected async delete(config?: AxiosRequestConfig<D>): Promise<AxiosResponse<R>> {
    return BaseService.axios.delete<R, AxiosResponse<R>, D>(this.path, config);
  }
}

export default BaseService;
