import { onBeforeMount, ref } from 'vue';

import { useRedirect } from '@/composable/navigation';

import { DataLayerInfoComposition } from './types';
import { RoutePaths } from '@/router/types';

export function useDataLayerInfo(): DataLayerInfoComposition {
  const redirect = useRedirect();

  const flowLabel = ref<FlowEventLabel | null>(null);

  function getFlowLabel(): FlowEventLabel {
    const redirectToPath = redirect.redirectToPath.value;

    if (redirectToPath.includes(RoutePaths.CONSULTATION)) {
      return 'my_services';
    }

    if (redirectToPath.includes(RoutePaths.CONSULTATION_CONCLUSION)) {
      return 'conclusion';
    }

    return 'telemed';
  }

  onBeforeMount(() => {
    flowLabel.value = getFlowLabel();
  });

  return {
    flowLabel,
  };
}
