import { onMounted } from 'vue';
import { useRoute } from 'vue-router';

import { useSimpleStore } from '@/utils/vuex';

import { FilesComposition } from './types';

export function useFiles(): FilesComposition {
  const store = useSimpleStore();
  const route = useRoute();

  onMounted(() => {
    store.files.clear();
    store.files.getFileById({
      fileId: route.params.fileId as string,
    });
  });

  return {
    files: store.files.state,
  };
}
