
import { Options, Vue } from 'vue-class-component';
import { ModelSync, Prop } from 'vue-property-decorator';

import { CalendarDateChangeOutput } from '@/ui/molecules/RCalendar/types';
import { ModalCalendarProps } from './types';

import { RDialog } from '@/ui/atoms';
import { RCalendar } from '@/ui/molecules';

@Options({
  components: {
    RDialog,
    RCalendar,
  },
})
export default class ModalCalendar extends Vue implements ModalCalendarProps {
  @ModelSync('value', 'change') modelValue!: boolean;

  @Prop({ type: Boolean, default: true }) closeOnDateChange!: boolean;

  @Prop(String) format!: string;

  @Prop(Array) availableDates!: Date[];

  handleCloseClick(event: any): void {
    // don`t propagate to v-calendar`s default nav click
    event.stopPropagation();

    this.$emit('update:modelValue', false);
  }

  handleDateChange(date: CalendarDateChangeOutput): void {
    if (this.closeOnDateChange) {
      this.$emit('update:modelValue', false);
    }
    this.$emit('onDateChange', date);
  }
}
