import { App } from 'vue';

import {
  pushEvent,
} from '@/utils';

export const analyticsUtils = {
  pushEvent,
};

export function initAnalyticsUtils(app: App<Element>): App<Element> {
  const clonedApp = { ...app };

  clonedApp.config.globalProperties.$analyticsUtils = analyticsUtils;

  return clonedApp;
}
