import { safeParse } from '@/utils';

import { BrowserStorageKeys } from '@/types/common';
import {
  AuthResponse, ConsultationDto, DoctorCommonDto, DoctorIntervalValueDto, SpecialityDto, UserInfoDto,
} from '@/types/models';

export function getBuduTokensFromBrowserStorage(): AuthResponse | null {
  const tokens = window.sessionStorage.getItem(BrowserStorageKeys.tokens);

  const {
    accessExpireTime,
    accessToken,
    refreshExpireTime,
    refreshToken,
  } = safeParse(tokens) ?? {} as AuthResponse;

  if (!accessToken || !accessExpireTime) {
    return null;
  }

  return new AuthResponse(
    refreshToken,
    accessToken,
    accessExpireTime,
    refreshExpireTime
  );
}

export function setTokensToBrowserStorage(tokens: AuthResponse): void {
  window.sessionStorage.setItem(BrowserStorageKeys.tokens, JSON.stringify(tokens));
}

export function removeTokensFromBrowserStorage(): void {
  window.sessionStorage.removeItem(BrowserStorageKeys.tokens);
}

export function getUserInfoFromBrowserStorage(): UserInfoDto | null {
  const userInfo = window.sessionStorage.getItem(BrowserStorageKeys.userInfo);

  return safeParse<UserInfoDto>(userInfo);
}

export function setUserInfoToBrowserStorage(userInfo: UserInfoDto): void {
  window.sessionStorage.setItem(BrowserStorageKeys.userInfo, JSON.stringify(userInfo));
}

export function removeUserInfoFromBrowserStorage(): void {
  window.sessionStorage.removeItem(BrowserStorageKeys.userInfo);
}

