import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router';

import { RouteNames, RoutePaths, WebviewPathsMessages } from './types';
import { Permissions } from '@/types/common';

import { getDoctorFromBrowserStorage, getSpecialityFromBrowserStorage } from '@/utils/storage';

const SpecialitiesList = (): Promise<unknown> => (
  import('@/pages/SpecialitiesList/SpecialitiesList.vue')
);
const DoctorsBySpecs = (): Promise<unknown> => (
  import('@/pages/DoctorsBySpecs/DoctorsBySpecs.vue')
);
const DoctorAppointment = (): Promise<unknown> => (
  import('@/pages/DoctorAppointment/DoctorAppointment.vue')
);
const Authorization = (): Promise<unknown> => (
  import('@/pages/Authorization/Authorization.vue')
);
const Consultation = (): Promise<unknown> => (
  import('@/pages/Consultation/Consultation.vue')
);
const UserData = (): Promise<unknown> => (
  import('@/pages/UserData/UserData.vue')
);
const ServiceConfirmation = (): Promise<unknown> => (
  import('@/pages/ServiceConfirmation/ServiceConfirmation.vue')
);
const InfoAfterPayment = (): Promise<unknown> => (
  import('@/pages/InfoAfterPayment/InfoAfterPayment.vue')
);
const NotFound = (): Promise<unknown> => (
  import('@/pages/NotFound/NotFound.vue')
);
const ConsultationCancel = (): Promise<unknown> => (
  import('@/pages/ConsultationCancel/ConsultationCancel.vue')
);
const ConsultationConclusion = (): Promise<unknown> => (
  import('@/pages/ConsultationConclusion/ConsultationConclusion.vue')
);
const DetailedError = (): Promise<unknown> => (
  import('@/pages/DetailedError/DetailedError.vue')
);
const PhoneNumberChange = (): Promise<unknown> => (
  import('@/pages/PhoneNumberChange/PhoneNumberChange.vue')
);

const routes: Array<RouteRecordRaw> = [
  {
    path: RoutePaths.SPECIALTIES,
    name: RouteNames.SPECIALTIES,
    component: SpecialitiesList,
    meta: {
      permissions: [
        Permissions.GUEST,
      ],
      navigationPanel: {
        goBackPath: (): string => WebviewPathsMessages.CLOSE_WEBVIEW,
      },
    },
  },
  {
    path: `${RoutePaths.DOCTORS_BY_SPECS}/:specialityId/:specialityName`,
    name: RouteNames.DOCTORS_BY_SPECS,
    component: DoctorsBySpecs,
    meta: {
      permissions: [
        Permissions.GUEST,
      ],
      navigationPanel: {
        goBackPath: (): string => RoutePaths.SPECIALTIES,
      },
    },
  },
  {
    path: `${RoutePaths.DOCTOR_APPOINTMENT}/:doctorId`,
    name: RouteNames.DOCTOR_APPOINTMENT,
    component: DoctorAppointment,
    meta: {
      permissions: [
        Permissions.GUEST,
      ],
      navigationPanel: {
        goBackPath: (): string => {
          const speciality = getSpecialityFromBrowserStorage();

          return `${RoutePaths.DOCTORS_BY_SPECS}/${speciality?.id}/${speciality?.specialityName}`;
        },
      },
    },
  },
  {
    path: RoutePaths.AUTHORIZATION,
    name: RouteNames.AUTHORIZATION,
    component: Authorization,
    meta: {
      permissions: [
        Permissions.GUEST,
      ],
      navigationPanel: {
        goBackPath: (_, route): string => route.redirectedFrom?.fullPath ?? RoutePaths.AUTHORIZATION,
      },
    },
  },
  {
    path: `${RoutePaths.CONSULTATION}/:consultationId`,
    name: RouteNames.CONSULTATION,
    component: Consultation,
    meta: {
      permissions: [
        Permissions.CODE_AUTHORIZED_USER,
      ],
      navigationPanel: {
        goBackPath: (): string => WebviewPathsMessages.CLOSE_WEBVIEW,
      },
    },
  },
  {
    path: RoutePaths.USER_DATA,
    name: RouteNames.USER_DATA,
    component: UserData,
    meta: {
      permissions: [
        Permissions.CODE_AUTHORIZED_USER,
      ],
      navigationPanel: {
        goBackPath: (): string => {
          const doctor = getDoctorFromBrowserStorage();

          return `${RoutePaths.DOCTOR_APPOINTMENT}/${doctor?.id}`;
        },
      },
    },
  },
  {
    path: `${RoutePaths.SERVICE_CONFIRMATION}/:specialityId/:productId/:intervalId`,
    name: RouteNames.SERVICE_CONFIRMATION,
    component: ServiceConfirmation,
    meta: {
      permissions: [
        Permissions.CODE_AUTHORIZED_USER,
      ],
      navigationPanel: {
        goBackPath: (): string => {
          const doctor = getDoctorFromBrowserStorage();

          return `${RoutePaths.DOCTOR_APPOINTMENT}/${doctor?.id}`;
        },
      },
    },
  },
  {
    path: RoutePaths.INFO_AFTER_PAYMENT,
    name: RouteNames.INFO_AFTER_PAYMENT,
    component: InfoAfterPayment,
    meta: {
      permissions: [
        Permissions.CODE_AUTHORIZED_USER,
      ],
    },
  },
  {
    path: RoutePaths.CONSULTATION_CANCEL,
    name: RouteNames.CONSULTATION_CANCEL,
    component: ConsultationCancel,
    meta: {
      permissions: [
        Permissions.CODE_AUTHORIZED_USER,
      ],
    },
  },
  {
    path: `${RoutePaths.CONSULTATION_CONCLUSION}/:fileId`,
    name: RouteNames.CONSULTATION_CONCLUSION,
    component: ConsultationConclusion,
    meta: {
      permissions: [
        Permissions.CODE_AUTHORIZED_USER,
      ],
    },
  },
  {
    path: RoutePaths.DETAILED_ERROR,
    name: RouteNames.DETAILED_ERROR,
    component: DetailedError,
    meta: {
      permissions: [
        Permissions.GUEST,
      ],
      navigationPanel: {
        goBackPath: (): string => RoutePaths.SPECIALTIES,
      },
    },
  },
  {
    path: RoutePaths.PHONE_NUMBER_CHANGE,
    name: RouteNames.PHONE_NUMBER_CHANGE,
    component: PhoneNumberChange,
    meta: {
      permissions: [
        Permissions.GUEST,
      ],
      navigationPanel: {
        goBackPath: (_, route): string => route.query.redirectTo as string ?? RoutePaths.PHONE_NUMBER_CHANGE,
      },
    },
  },
  {
    path: RoutePaths.NOT_FOUND,
    name: RouteNames.NOT_FOUND,
    component: NotFound,
    meta: {
      permissions: [
        Permissions.GUEST,
      ],
      navigationPanel: {
        goBackPath: (): string => RoutePaths.SPECIALTIES,
      },
    },
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
