
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { RIconProps, IconNames } from './types';

@Options({})
export default class RIcon extends Vue implements RIconProps {
  @Prop({ default: '' }) readonly name!: IconNames;

  @Prop({ default: '1em' }) readonly height!: string;

  @Prop({ default: '1em' }) readonly width!: string;
}
