import reduce from 'lodash/reduce';

import { DmTokensUrlParams, UrlSearchKeys } from '@/types/common';
import { AuthResponse } from '@/types/models';
import { Dictionary, ReflectType } from '@/utils/types';

export function getBuduTokensFromQueryParams(): AuthResponse | null {
  const params = new URLSearchParams(window.location.search);

  const accessToken = params.get(UrlSearchKeys.accessToken);
  const accessExpireTime = params.get(UrlSearchKeys.accessExpireTime) ?? '';
  const refreshToken = params.get(UrlSearchKeys.refreshToken);
  const refreshExpireTime = params.get(UrlSearchKeys.refreshExpireTime) ?? '';

  if (!accessToken || !accessExpireTime) {
    return null;
  }

  return new AuthResponse(
    refreshToken,
    accessToken,
    accessExpireTime,
    refreshExpireTime
  );
}

export function getDmTokensFromQueryParams(): DmTokensUrlParams | null {
  const params = new URLSearchParams(window.location.search);

  const dmAccessToken = params.get(UrlSearchKeys.dmAccessToken);

  if (!dmAccessToken) {
    return null;
  }

  return {
    dmAccessToken,
  };
}

export function createUrlSearchParams<T extends Dictionary<string | number>>(queryParams: ReflectType<T>): string {
  const urlSearchParams = reduce(queryParams, (urlParams, queryParam, queryParamName) => {
    if (queryParam) {
      urlParams.append(queryParamName, queryParam.toString());
    }

    return urlParams;
  }, new URLSearchParams());

  const delimiter = window.location.search.length ? '&' : '?';

  return `${delimiter}${urlSearchParams.toString()}`;
}
