
import { Options, Vue } from 'vue-class-component';
import { ModelSync, Prop } from 'vue-property-decorator';

@Options({})
export default class RCheckbox extends Vue {
  @ModelSync('checked', 'change', { default: false }) modelValue!: boolean;

  @Prop() readonly label!: string;

  @Prop() readonly size!: string;

  @Prop() readonly color!: string;

  @Prop() readonly dense!: string;

  @Prop(Boolean) readonly disable!: boolean;
}
