
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import moment, { Moment } from 'moment';

import { Calendar } from 'v-calendar';
import { RSeparator, RBtn } from '@/ui/atoms';

import {
  CalendarRef, ClickedDay, RCalendarProps, TitlePosition, ToPage,
} from './types';

@Options({
  components: {
    Calendar,
    RSeparator,
    RBtn,
  },
})
export default class RCalendar extends Vue implements RCalendarProps {
  @Prop({ type: String, default: 'left' }) titlePosition!: TitlePosition;

  @Prop({ type: Boolean, default: true }) noBorder!: boolean;

  @Prop({ type: String, default: 'YYYY-MM-DD' }) format!: string;

  @Prop(Array) availableDates!: Date[];

  date: Moment = moment();

  goToPreviousMonth(): void {
    this.date = this.date.clone().subtract(1, 'month');

    (this.$refs.calendar as CalendarRef).move(this.date.toDate());
  }

  goToNextMonth(): void {
    this.date = this.date.clone().add(1, 'month');

    (this.$refs.calendar as CalendarRef).move(this.date.toDate());
  }

  handleDayClick(clickedDay: ClickedDay): void {
    this.$emit('onDateChange', moment(clickedDay.date).format(this.format));
  }

  handleToPageUpdate({ year, month }: ToPage): void {
    const momentMonth = month - 1;

    this.date = moment({ year, month: momentMonth });
  }
}
