import { computed } from 'vue';
import { useRouter } from 'vue-router';

import { useSimpleStore } from '@/utils/vuex';

import { UserComposition, UserDataUpdateComposition, UserInfoComposition } from './types';
import { RouteNames } from '@/router/types';
import { UserDataDto, UserInfoDto } from '@/types/models';

export function useCheckUserProfile(): UserComposition {
  const store = useSimpleStore();
  const router = useRouter();

  const needToUpdateProfileState = computed(() => store.user.needToUpdateProfile);
  const decodedToken = computed(() => store.authentication.decodedToken);
  const isNewUser = computed(() => store.user.isNewUser);

  async function skipProfileUpdateIfUpdated(): Promise<void> {
    if (decodedToken.value?.UserProfile !== 'ClientProfile') {
      router.push({ name: RouteNames.USER_DATA });
      return;
    }

    await store.user.getNeedToUpdateProfile();

    const { error, needToUpdateProfile } = needToUpdateProfileState.value;

    if (error) {
      return;
    }

    if (needToUpdateProfile) {
      router.push({ name: RouteNames.USER_DATA });
      return;
    }

    if (store.consultationInfo.canConfirmConsultation) {
      const { speciality, interval } = store.consultationInfo;

      router.push({
        name: RouteNames.SERVICE_CONFIRMATION,
        params: {
          specialityId: speciality?.id,
          productId: speciality?.productId,
          intervalId: interval?.id,
        },
      });
    }
  }

  return {
    getNeedToUpdateProfile: store.user.getNeedToUpdateProfile,
    isNewUser,
    needToUpdateProfile: needToUpdateProfileState.value,
    skipProfileUpdateIfUpdated,
  };
}

export function useUpdateUserData(): UserDataUpdateComposition {
  const store = useSimpleStore();

  async function updateUserData(data: UserDataDto): Promise<void> {
    await store.user.updateUserData(data);
  }

  return {
    userData: store.user.state,
    updateUserData,
  };
}

export function useUserInfo(): UserInfoComposition {
  const store = useSimpleStore();

  function updatePhoneNumber(PhoneNumber: string): void {
    store.userInfo.saveUserInfoToStoreAndBrowserStorage({
      ...(store.userInfo.state.data ?? {} as UserInfoDto),
      PhoneNumber,
    });
  }

  return {
    userInfo: store.userInfo.state,
    updatePhoneNumber,
  };
}
