/* eslint-disable @typescript-eslint/camelcase */
import { onMounted, computed } from 'vue';
import { useRoute } from 'vue-router';

import { useSimpleStore } from '@/utils/vuex';
import { pushEvent } from '@/utils';

import { ConsultationComposition } from './types';

export function useConsultation(): ConsultationComposition {
  const store = useSimpleStore();
  const route = useRoute();

  const conclusionFile = computed(() => store.consultation.conclusionFile);

  async function getConsultation(): Promise<void> {
    store.consultation.clear();

    return store.consultation.getConsultation({
      consultationId: route.params.consultationId as string,
    });
  }

  async function cancelConsultation(): Promise<void> {
    await store.consultation.cancelConsultation({
      consultationId: route.params.consultationId as string,
    });
  }

  onMounted(async () => {
    await getConsultation();
    store.consultationInfo
      .saveConsultationToStoreAndBrowserStorage(store.consultation.state.data);

    if (store.consultation.state.error) {
      return;
    }

    pushEvent('select_feed_list', {
      eventCategory: 'consultation',
      consultation_guid: store.consultation.state.data?.id as string,
      doctor_guid: store.consultation.state.data?.doctor.id as string,
    });
  });

  return {
    consultationCancel: store.consultation.consultationCancel,
    consultation: store.consultation.state,
    conclusionFile,
    cancelConsultation,
  };
}
