import { App } from 'vue';

import { masks as masksDictionary, isValidBirthDate, isValidPhoneNumber } from '@/utils';

export const masks = {
  masksDictionary,
};

export const validators = {
  isValidBirthDate,
  isValidPhoneNumber,
};

export function initValidationsUtils(app: App<Element>): App<Element> {
  const clonedApp = { ...app };

  clonedApp.config.globalProperties.$masks = masks;

  clonedApp.config.globalProperties.$validators = validators;

  return clonedApp;
}
