import { State, Mutation, Action } from 'vuex-simple';

import { ModalsModel, ServerErrorModalState } from './types';

class ModalsStore implements ModalsModel {
  @State()
  serverErrorModal: ServerErrorModalState = {
    show: false,
  }

  @Mutation()
  setServerErrorModal(serverErrorModalState: ServerErrorModalState): void {
    this.serverErrorModal = serverErrorModalState;
  }

  @Action()
  updateServerErrorModalShow = (serverErrorModalState: ServerErrorModalState): void => {
    this.setServerErrorModal(serverErrorModalState);
  }
}

export default ModalsStore;
