import Axios, { AxiosInstance, AxiosError } from 'axios';

import identity from 'lodash/identity';

import store from '@/store';

import { defaultRequestErrorMessage, noNotifyPaths } from '@/common';

import { notifyError } from '@/utils';

import { ErrorResponse } from '@/types/models';

import RequestsAborterInstance from './RequestsAborter';

export type InterceptorId = number;

export function initAuthenticationInterceptor(axios: AxiosInstance): InterceptorId {
  return axios.interceptors.request.use(
    config => {
      const accessToken = store.state.authentication.state.data?.accessToken;

      Axios.defaults.headers.common.Authorization = `Bearer ${accessToken}`;

      return {
        ...config,
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        signal: RequestsAborterInstance.signal,
      };
    },
    (error: AxiosError) => Promise.reject(error)
  );
}

export function initNotificationsInterceptor(axios: AxiosInstance): InterceptorId {
  return axios.interceptors.response.use(
    identity,
    (error: AxiosError) => {
      const { response, config } = error;
      const errorResponse = response?.data as ErrorResponse;

      const method = config?.method?.toLowerCase() as keyof typeof noNotifyPaths;
      const pathsByMethod = noNotifyPaths[method] ?? [];

      const shouldExcludeNotify = pathsByMethod
        .some(urlRegexp => urlRegexp.test(config.url ?? ''));

      if (!shouldExcludeNotify) {
        notifyError({
          group: errorResponse?.error?.code ?? error.code,
          message: errorResponse?.error?.description ?? defaultRequestErrorMessage,
        });
      }

      return Promise.reject(error);
    }
  );
}
