import { State, Mutation, Action } from 'vuex-simple';

import { FilesModel, FilesState } from './types';
import { FileParams } from '@/services/files/types';

import FilesServiceInstance from '@/services/files';

class FilesStore implements FilesModel {
  @State()
  state: FilesState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @Mutation()
  setFilesDataIsLoading(isLoading: boolean): void {
    this.state.isLoading = isLoading;
  }

  @Mutation()
  setFilesDataError(error: string): void {
    this.state.error = error;
  }

  @Mutation()
  setFilesData(data: Blob | null): void {
    this.state.data = data;
  }

  @Action()
  getFileById = async (params: FileParams): Promise<void> => {
    this.setFilesDataIsLoading(true);
    this.setFilesDataError('');
    try {
      const data = await FilesServiceInstance.getFileById({ params });

      this.setFilesData(data);
    } catch (error: any) {
      this.setFilesDataError(error.message);
    } finally {
      this.setFilesDataIsLoading(false);
    }
  }

  @Action()
  clear = (): void => {
    this.setFilesData(null);
  }
}

export default FilesStore;
