import compact from 'lodash/compact';

import { formatDate } from '@/utils/date';

export function getExperienceRangeDates(dateStart: string | null, dateEnd: string | null): string {
  const dates = [
    dateStart,
    dateEnd,
  ];

  return compact(dates)
    .map(date => formatDate(date, 'YYYY'))
    .join('-');
}
