import { onMounted, onUnmounted } from 'vue';
import { useRoute } from 'vue-router';
import { useSimpleStore } from '@/utils/vuex';

import { ConfirmationInfoComposition } from './types';

export function useConfirmationInfo(): ConfirmationInfoComposition {
  const store = useSimpleStore();
  const route = useRoute();

  onMounted(() => {
    store.confirmationInfo.getConfirmationInfo({
      SpecialityId: route.params.specialityId as string,
      ProductId: route.params.productId as string,
      IntervalId: route.params.intervalId as string,
    });
  });
  onUnmounted(() => {
    store.confirmationInfo.clear();
  });

  return {
    confirmationInfo: store.confirmationInfo.state,
  };
}
