import { AxiosRequestConfig } from 'axios';
import { BaseService } from './BaseService';

export class CrudService<R, D> extends BaseService<R, D> {
  public async create(data: D, config?: AxiosRequestConfig<D>): Promise<R | never> {
    try {
      const response = await this.post(data, config);

      return response.data;
    } catch (error: any) {
      throw new Error(`Ошибка создания ${this.path}: ${error.message}`);
    }
  }

  public async read(config?: AxiosRequestConfig): Promise<R | R[] | never> {
    try {
      const response = await this.get(config);

      return response.data;
    } catch (error: any) {
      throw new Error(`Ошибка получения ${this.path}: ${error.message}`);
    }
  }

  public async update(data: D, config?: AxiosRequestConfig<D>): Promise<R | never> {
    try {
      const response = await this.patch(data, config);

      return response.data;
    } catch (error: any) {
      throw new Error(`Ошибка обновления ${this.path}: ${error.message}`);
    }
  }

  public async remove(config?: AxiosRequestConfig<D>): Promise<R | never> {
    try {
      const response = await super.delete(config);

      return response.data;
    } catch (error: any) {
      throw new Error(`Ошибка удаления ${this.path}: ${error.message}`);
    }
  }
}

export default CrudService;
