/* eslint-disable @typescript-eslint/explicit-function-return-type */
import { onMounted, onUnmounted } from 'vue';

import { useSimpleStore } from '@/utils/vuex';
import { SpecialitiesComposition } from './types';

export function useSpecialities(): SpecialitiesComposition {
  const store = useSimpleStore();

  onMounted(() => store.specialities.getSpecialities());
  onUnmounted(() => store.specialities.clear());

  return {
    specialities: store.specialities.state,
  };
}
