
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { TimeBadgeProps } from './types';

import RSpinner from '@/ui/atoms/RSpinner/RSpinner.vue';

@Options({
  components: {
    RSpinner,
  },
})
export default class TimeBadge extends Vue implements TimeBadgeProps {
  @Prop({ default: '64px' }) readonly width!: string;

  @Prop({ default: '44px' }) readonly height!: string;

  @Prop({ type: Boolean, default: false }) readonly disabled!: boolean;

  @Prop(Boolean) readonly loading!: boolean;

  @Prop(Boolean) readonly readonly!: boolean;
}
