import { onMounted, computed, onUnmounted } from 'vue';

import { useSimpleStore } from '@/utils/vuex';

import RequestsAborterInstance from '@/api/RequestsAborter';

import {
  AuthorizationComposition,
  AuthorizationCooldownComposition,
  AuthorizationConformationComposition,
} from './types';
import { AuthDeviceInfoDto } from '@/types/models';

export function useSmsCodeAuthorization(): AuthorizationComposition {
  const store = useSimpleStore();
  const phoneNumber = store.userInfo.state?.data?.PhoneNumber;

  async function tryAuthorize(phoneNumber: string | undefined): Promise<void> {
    if (!phoneNumber) {
      return;
    }

    await store.authorization.sendSms({
      phoneNumber,
      appInfoDto: new AuthDeviceInfoDto('f8c72ea5-1f78-4c8e-b999-77eb813e3722'),
    });
  }

  onMounted(() => {
    tryAuthorize(phoneNumber);
  });

  onUnmounted(() => {
    RequestsAborterInstance.abortRequests();
    store.authorization.clear();
  });

  return {
    authorize: (): Promise<void> => tryAuthorize(phoneNumber),
    isLoading: computed(() => store.authorization.smsSending.isLoading),
  };
}

export function useSmsCodeConfirmation(): AuthorizationConformationComposition {
  const store = useSimpleStore();
  const confirmationToken = computed(() => store.authorization.smsSending.data?.confirmationToken);

  async function authorizeByConfirmationCode(confirmationCode: string): Promise<void> {
    if (!confirmationToken.value) {
      return;
    }

    await store.authorization.confirmSms({
      confirmationToken: confirmationToken.value,
      confirmationCode,
    });
  }

  return {
    authorizationConfirmation: store.authorization.smsConfirmation,
    authorizeByConfirmationCode,
  };
}

export function useSmsCodeCooldown(): AuthorizationCooldownComposition {
  const store = useSimpleStore();
  const remainingTimeSeconds = computed(() => (
    store.authorization.remainingTimeSeconds
  ));

  const tick = setInterval(() => {
    if (remainingTimeSeconds.value !== null && remainingTimeSeconds.value > 0) {
      store.authorization.decreaseRemainingTime();
    }
  }, 1000);

  onUnmounted(() => clearInterval(tick));

  return {
    remainingTimeSeconds,
  };
}
