import { App } from 'vue';

import { RouteNames, RoutePaths, WebviewPathsMessages } from '@/router/types';

export function initRoutes(app: App<Element>): App<Element> {
  const clonedApp = { ...app };

  clonedApp.config.globalProperties.$routeNames = RouteNames;
  clonedApp.config.globalProperties.$routePaths = RoutePaths;
  clonedApp.config.globalProperties.$webviewPathsMessages = WebviewPathsMessages;

  return clonedApp;
}
