
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { RSpinnerProps } from './types';

@Options({})
export default class RSpinner extends Vue implements RSpinnerProps {
  @Prop({ default: 'primary' }) readonly color!: string;

  @Prop({ default: '30' }) readonly size!: string;

  @Prop({ default: 5 }) readonly thickness!: number;
}
