import { App } from 'vue';

import {
  createUrlSearchParams,
} from '@/utils';

export const urlUtils = {
  createUrlSearchParams,
};

export function initUrlUtils(app: App<Element>): App<Element> {
  const clonedApp = { ...app };

  clonedApp.config.globalProperties.$urlUtils = urlUtils;

  return clonedApp;
}
