
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { RBadgeProps } from './types';

@Options({})
export default class RBadge extends Vue implements RBadgeProps {
  @Prop() readonly label!: string;

  @Prop({ type: Boolean, default: true }) readonly rRounded!: boolean

  @Prop({ type: Boolean, default: false }) readonly bordered!: boolean;

  @Prop({ type: Boolean, default: false }) readonly disabled!: boolean;

  @Prop(String) readonly height!: string;

  @Prop(String) readonly width!: string;
}
