import get from 'lodash/get';
import compact from 'lodash/compact';

import { FullNamePaths } from './types';

export function getFullName(entity: any, paths: FullNamePaths = {} as FullNamePaths): string {
  const {
    firstNamePath = 'firstName',
    middleNamePath = 'middleName',
    lastNamePath = 'lastName',
  } = paths;

  const fullNameRaw = [
    get(entity, firstNamePath),
    get(entity, middleNamePath),
    get(entity, lastNamePath),
  ];

  return compact(fullNameRaw).join(' ').trim();
}

export function formatPhoneNumber(phoneNumber: string | null): string {
  if (!phoneNumber) {
    return '';
  }

  return phoneNumber.replace(
    /(\d)(\d\d\d)(\d\d\d)(\d\d)(\d\d)/,
    '+$1 ($2) $3-$4-$5'
  );
}

export function safeParse<T>(json: any, safe = true): T | null {
  try {
    return JSON.parse(json);
  } catch (e) {
    if (safe) {
      return null;
    }
    throw new Error(`You're trying to decode an invalid JSON String: ${json}`);
  }
}
