
import { Options, setup, Vue } from 'vue-class-component';

import { getDmTokensFromQueryParams } from './utils';

import { useAuthentication, useInitialSetup, useModals } from './composable';

// eslint-disable-next-line import/extensions
import '@/styles/app.scss';

import { RBtn, RDialog } from '@/ui/atoms';
import { ModalSpinner, ModalServerError } from '@/ui/organisms';

// todo fix transitions
@Options({
  components: {
    RBtn,
    ModalSpinner,
    ModalServerError,
    RDialog,
  },
})
export default class App extends Vue {
  authentication = setup(useAuthentication);

  initialSetup = setup(useInitialSetup);

  modals = setup(useModals);

  dmAccessTokenModalShow = false;

  currentUrlModalShow = false;

  // eslint-disable-next-line class-methods-use-this
  getDmAccessToken(): string {
    return getDmTokensFromQueryParams()?.dmAccessToken ?? 'Токен не найден';
  }

  // eslint-disable-next-line class-methods-use-this
  getCurrentUrl(): string {
    return window.location.href;
  }

  get reloadOnErrorUrl(): string {
    return this.$route.name === this.$routeNames.NOT_FOUND
      ? this.initialSetup.redirectedFrom
      : '';
  }
}
