import moment from 'moment';

import { defaultRegionCode } from '@/common';

export function isValidPhoneNumber(value: string): boolean {
  return value.startsWith(`${defaultRegionCode}9`);
}

export function isValidBirthDate(date: string): boolean {
  const regexDate = /^\d{2}.\d{2}.\d{4}$/;

  if (!regexDate.test(date)) {
    return false;
  }

  const dateForComparison = moment(date, 'DD.MM.YYYY');

  return dateForComparison.isValid() && dateForComparison.isBetween('1900-01-01', moment());
}
