
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { RCardProps } from './types';

@Options({})
export default class RCard extends Vue implements RCardProps {
  @Prop({ default: true }) readonly flat!: boolean;

  @Prop({ default: false }) readonly bordered!: boolean;

  @Prop({ default: '85%' }) readonly width!: string;
}
