import { State, Mutation, Action } from 'vuex-simple';

import { Store } from '@/store';

import { SpecialitiesModel, SpecialitiesState } from './types';
import { SpecialityDto } from '@/types/models';

import SpecialitiesServiceInstance from '@/services/specialities';

class SpecialitiesStore implements SpecialitiesModel {
  // eslint-disable-next-line no-useless-constructor
  constructor(private root: Store) {}

  @State()
  state: SpecialitiesState = {
    data: [],
    error: '',
    isLoading: false,
  }

  @Mutation()
  setSpecialitiesIsLoading(isLoading: boolean): void {
    this.state.isLoading = isLoading;
  }

  @Mutation()
  setSpecialitiesError(error: string): void {
    this.state.error = error;
  }

  @Mutation()
  setSpecialitiesData(data: SpecialityDto[]): void {
    this.state.data = data;
  }

  @Action()
  getSpecialities = async (): Promise<void> => {
    this.setSpecialitiesIsLoading(true);
    this.setSpecialitiesError('');
    this.root.modals.updateServerErrorModalShow({
      show: false,
    });
    try {
      const specialities = await SpecialitiesServiceInstance.getSpecialities();

      this.setSpecialitiesData(specialities);
    } catch (error: any) {
      this.setSpecialitiesError(error.message);
      this.root.modals.updateServerErrorModalShow({
        show: true,
      });
    } finally {
      this.setSpecialitiesIsLoading(false);
    }
  }

  @Action()
  clear = async (): Promise<void> => {
    this.setSpecialitiesData([]);
  }
}

export default SpecialitiesStore;
