/* eslint-disable @typescript-eslint/explicit-function-return-type */
import { computed, onMounted } from 'vue';

import { useSimpleStore } from '@/utils/vuex';

import { ConsultationInfoComposition } from './types';

export function useConsultationInfo(): ConsultationInfoComposition {
  const store = useSimpleStore();

  const consultationInfoStore = computed(() => store.consultationInfo);

  onMounted(() => {
    consultationInfoStore.value.tryLoadConsultationInfoFromBrowserStorage();
  });

  return {
    consultationInfoStore: consultationInfoStore.value,
  };
}
