
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { DefaultLayoutProps } from './types';

@Options({})
export default class DefaultLayout extends Vue implements DefaultLayoutProps {
  @Prop({ default: '' }) title!: string;

  @Prop({ type: Boolean, default: true }) readonly overlayedTitle!: boolean;
}
