import { State, Mutation, Action } from 'vuex-simple';

import {
  getBuduTokensFromBrowserStorage,
  getDmTokensFromQueryParams,
  getUserInfoFromBrowserStorage,
  setUserInfoToBrowserStorage,
} from '@/utils';

import {
  UserInfoModel, UserInfoState,
} from './types';
import { UserInfoDto } from '@/types/models';

import UserServiceInstance from '@/services/user';

class UserInfoStore implements UserInfoModel {
  @State()
  state: UserInfoState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @Mutation()
  setUserInfoIsLoading(isLoading: boolean): void {
    this.state.isLoading = isLoading;
  }

  @Mutation()
  setUserInfoError(error: string): void {
    this.state.error = error;
  }

  @Mutation()
  setUserInfoData(data: UserInfoDto | null): void {
    this.state.data = data;
  }

  @Action()
  loadUserInfo = (): void => {
    try {
      this.tryLoadUserInfoFromBrowserStorage();
    } catch {
      const dmTokens = getDmTokensFromQueryParams();
      const buduTokens = getBuduTokensFromBrowserStorage();

      if (dmTokens?.dmAccessToken) {
        this.tryRequestUserInfoByDmToken(dmTokens.dmAccessToken);
      } else if (buduTokens?.accessToken) {
        this.tryRequestUserInfoByBuduToken(buduTokens.accessToken);
      }
    }
  }

  @Action()
  tryLoadUserInfoFromBrowserStorage = (): void | never => {
    const userInfo = getUserInfoFromBrowserStorage();

    if (userInfo) {
      this.saveUserInfoToStoreAndBrowserStorage(userInfo);
    } else {
      throw new Error('No user data/valid user data in browser storage');
    }
  }

  @Action()
  tryRequestUserInfoByDmToken = async (detMirToken: string): Promise<void | never> => {
    this.setUserInfoIsLoading(true);
    this.setUserInfoError('');
    try {
      const userInfo = await UserServiceInstance.getUserInfoByDmToken({ detMirToken });

      this.saveUserInfoToStoreAndBrowserStorage(userInfo);
    } catch (error: any) {
      this.setUserInfoError(error.message);
    } finally {
      this.setUserInfoIsLoading(false);
    }
  }

  @Action()
  tryRequestUserInfoByBuduToken = async (buduToken: string): Promise<void | never> => {
    this.setUserInfoIsLoading(true);
    this.setUserInfoError('');
    try {
      const userInfo = await UserServiceInstance.getUserInfoByBuduToken({ buduToken });

      this.saveUserInfoToStoreAndBrowserStorage(userInfo);
    } catch (error: any) {
      this.setUserInfoError(error.message);
    } finally {
      this.setUserInfoIsLoading(false);
    }
  }

  @Action()
  saveUserInfoToStoreAndBrowserStorage = (userInfo: UserInfoDto): void => {
    this.setUserInfoData(userInfo);
    setUserInfoToBrowserStorage(userInfo);
  }

  @Action()
  clear = async (): Promise<void> => {
    this.setUserInfoData(null);
  }
}

export default UserInfoStore;
