import { AxiosRequestConfig } from 'axios';

import {
  DoctorCommonParams,
  DoctorParams,
  AppointmentIntervalsParams,
  AppointmentIntervalParams,
  DoctorCommentsParams,
  DoctorExperienceParams,
} from './types';
import {
  DoctorCommonDto,
  ItemsDto,
  DoctorDetailedDto,
  DoctorIntervalDto,
  IntervalDto,
  DoctorCommentDto,
  DoctorExperienceAndEducationDto,
} from '@/types/models';

import { BaseService, CrudService } from '@/api';

export class DoctorsService<T, D> extends CrudService<T, D> {
  constructor() {
    super('/Doctors');
  }

  // eslint-disable-next-line arrow-body-style
  public getDoctors = async (config?: AxiosRequestConfig<DoctorCommonParams>): Promise<T> => {
    return this.read(config) as Promise<T>;
  }

  public async getDoctor(config?: AxiosRequestConfig<DoctorParams>): Promise<DoctorDetailedDto> {
    const response = await BaseService.axios.get(`${this.path}/${config?.params.doctorId}`);

    return response.data;
  }

  public async getAppointmentIntervals(config?: AxiosRequestConfig<AppointmentIntervalsParams>): Promise<DoctorIntervalDto[]> {
    const response = await BaseService.axios.get(`${this.path}/intervals`, config);

    return response.data;
  }

  public async getAppointmentInterval(config?: AxiosRequestConfig<AppointmentIntervalParams>): Promise<IntervalDto> {
    const response = await BaseService.axios.get(`${this.path}/intervals/${config?.params.intervalId}`, config);

    return response.data;
  }

  public getDoctorComments = async (config?: AxiosRequestConfig<DoctorCommentsParams>): Promise<ItemsDto<DoctorCommentDto>> => {
    const response = await BaseService.axios.get(`${this.path}/${config?.params.doctorId}/comments`, config);

    return response.data;
  }

  public getDoctorExperience = async (config?: AxiosRequestConfig<DoctorExperienceParams>): Promise<DoctorExperienceAndEducationDto> => {
    const response = await BaseService.axios.get(`${this.path}/${config?.params.doctorId}/Experience`, config);

    return response.data;
  }
}

export default new DoctorsService<ItemsDto<DoctorCommonDto>, unknown>();
