import { AxiosRequestConfig } from 'axios';
import { BaseService, CrudService } from '@/api';

import { AuthDeviceInfoDto, AuthResponse } from '@/types/models';
import { AuthByRefreshTokenRequest } from './types';

export class AuthorizationService<T, D> extends CrudService<T, D> {
  constructor() {
    super('/Auth');
  }

  public async loginAsGuest(data: AuthDeviceInfoDto, config?: AxiosRequestConfig<AuthDeviceInfoDto>): Promise<AuthResponse> {
    const response = await BaseService.axios.post(`${this.path}/guest`, data, config);

    return response.data;
  }

  public async refreshToken(
    data: AuthByRefreshTokenRequest,
    config?: AxiosRequestConfig<AuthByRefreshTokenRequest>
  ): Promise<AuthResponse> {
    const response = await BaseService.axios.post(`${this.path}/refresh`, data, config);

    return response.data;
  }
}

export default new AuthorizationService<any, any>();
