// eslint-disable-next-line no-shadow
export enum UserAppType {
  ANDROID = 'AndroidApp',
  IOS = 'IOSApp',
  WEBCLIENT = 'WebClientApp',
  WEBADMIN = 'WebAdminApp',
  UNKNOWN = 'Unknown',
}

// eslint-disable-next-line no-shadow
export enum UserAppCode {
  RENHEALTH = 'RenHealth',
  BUDU = 'Budu',
}

export class AuthDeviceInfoDto {
  // eslint-disable-next-line no-useless-constructor
  constructor(
    public deviceId: string,
    public userAppType: UserAppType = UserAppType.UNKNOWN,
    public deviceName: string = '',
    public version: string = '',
    public userAppCode: UserAppCode = UserAppCode.BUDU
  ) {}
}
