import { computed, onMounted, ref } from 'vue';
import { useRouter } from 'vue-router';

import { useSimpleStore } from '@/utils/vuex';
import { NavigationGuard } from '@/utils';

import { InitialSetupComposition } from './types';
import { DecodedJwtToken } from '@/types/models';

export function useInitialSetup(): InitialSetupComposition {
  const router = useRouter();
  const store = useSimpleStore();

  const decodedToken = computed<DecodedJwtToken | null>(() => store.authentication.decodedToken);
  const redirectedFrom = ref('');

  async function safeLoadTokensAndUserInfo(): Promise<void> {
    // first load, try to get tokens from url query params
    await store.authentication.loadUserTokens();

    store.userInfo.loadUserInfo();
  }

  safeLoadTokensAndUserInfo();

  router.beforeEach(async (to, from, next) => {
    const guardFactory = new NavigationGuard(store, { to, from, next });
    const navigationGuard = guardFactory.createGuard(decodedToken.value?.UserProfile);
    const nextRouteName = await navigationGuard.nextName();

    if (nextRouteName !== to.name) {
      redirectedFrom.value = to.path;

      next({ name: nextRouteName });
      return;
    }

    next();
  });

  onMounted(() => {
    store.consultationInfo.tryLoadConsultationInfoFromBrowserStorage();
  });

  return {
    redirectedFrom,
  };
}
