import { Quasar } from 'quasar';
import { createApp } from 'vue';
import compose from 'lodash/fp/compose';

import App from './App.vue';

import router from './router';
import store from './store';

import { getQuasarUserOptions } from './quasar-user-options';

import {
  DefaultLayout, InfoLayout, ModalLayout, NavigationLayout,
} from './ui/layout';
import { RIcon, Typography } from './ui/atoms';

import { initAuthenticationInterceptor, BaseService, initNotificationsInterceptor } from './api';
import {
  initFilters,
  initMomentSettings,
  initRoutes,
  initFilesUtils,
  initTheming,
  initUrlUtils,
  initUiUtils,
  initAnalyticsUtils,
} from './plugins';
import { initCustomDirectives } from './directives';
import { initValidationsUtils } from './plugins/validations';

import { getThemeFromSearch } from './utils';

initMomentSettings();
initAuthenticationInterceptor(BaseService.axios);
initNotificationsInterceptor(BaseService.axios);

const createEnhancedApp = compose(
  initAnalyticsUtils,
  initUiUtils,
  initTheming,
  initUrlUtils,
  initValidationsUtils,
  initCustomDirectives,
  initFilesUtils,
  initRoutes,
  initFilters,
  createApp
);

const app = createEnhancedApp(App);

app.component('DefaultLayout', DefaultLayout);
app.component('ModalLayout', ModalLayout);
app.component('InfoLayout', InfoLayout);
app.component('NavigationLayout', NavigationLayout);
app.component('RIcon', RIcon);
app.component('Typography', Typography);

const quasarUserOptions = compose(getQuasarUserOptions, getThemeFromSearch);

app
  .use(Quasar, quasarUserOptions())
  .use(store)
  .use(router)
  .mount('#app');
