import {
  State, Mutation, Action,
} from 'vuex-simple';

import { Store } from '@/store';

import { ConfirmationInfoDto } from '@/types/models';

import { ConfirmationInfoModel, ConfirmationInfoState } from './types';
import { ConfirmationInfoRequest } from '@/services/orders/types';

import OrdersServiceInstance from '@/services/orders';

class ConfirmationInfoStore implements ConfirmationInfoModel {
  // eslint-disable-next-line no-useless-constructor
  constructor(private root: Store) {}

  @State()
  state: ConfirmationInfoState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @Mutation()
  setConfirmationInfoData(data: ConfirmationInfoDto | null): void {
    this.state.data = data;
  }

  @Mutation()
  setConfirmationInfoError(error: string): void {
    this.state.error = error;
  }

  @Mutation()
  setConfirmationInfoIsLoading(isLoading: boolean): void {
    this.state.isLoading = isLoading;
  }

  @Action()
  getConfirmationInfo = async (requestParams: ConfirmationInfoRequest): Promise<void> => {
    this.setConfirmationInfoIsLoading(true);
    this.setConfirmationInfoError('');
    this.root.modals.updateServerErrorModalShow({
      show: false,
    });
    try {
      const confirmationInfo = await OrdersServiceInstance.getConfirmationInfo({ params: requestParams });

      this.setConfirmationInfoData(confirmationInfo);
    } catch (error: any) {
      this.setConfirmationInfoError(error.message);
      this.root.modals.updateServerErrorModalShow({
        show: true,
      });
    } finally {
      this.setConfirmationInfoIsLoading(false);
    }
  }

  @Action()
  clear = async (): Promise<void> => {
    this.setConfirmationInfoData(null);
  }
}

export default ConfirmationInfoStore;
