import { AxiosRequestConfig } from 'axios';
import { BaseService, CrudService } from '@/api';

import { ConsultationDto } from '@/types/models';
import { ConsultationViewParams, ConsultationCancelParams } from './types';

export class ConsultationService<T, D> extends CrudService<T, D> {
  constructor() {
    super('/Consultation');
  }

  public async getConsultation(config?: AxiosRequestConfig<ConsultationViewParams>): Promise<ConsultationDto> {
    const response = await BaseService.axios.get(`${this.path}/${config?.params?.consultationId}`);

    return response.data;
  }

  public async cancelConsultation(config?: AxiosRequestConfig<ConsultationCancelParams>): Promise<ConsultationDto> {
    const response = await BaseService.axios.get(`${this.path}/${config?.params?.consultationId}/cancel`);

    return response.data;
  }
}

export default new ConsultationService<ConsultationDto, unknown>();
