
import { Options, Vue } from 'vue-class-component';
import { ModelSync, Prop } from 'vue-property-decorator';

import { RTabsProps, QTabsModelValue } from './types';

@Options({})
export default class RTabs extends Vue implements RTabsProps {
  @ModelSync('value', 'input', { type: [ Number, String, null ] }) modelValue: QTabsModelValue;

  @Prop({ type: Boolean, default: false }) readonly outsideArrows!: boolean;

  @Prop(Boolean) readonly disable!: boolean;

  handleTabUpdate(tabName: string): void {
    this.$emit('onTabUpdate', tabName);
  }
}
