
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { ModalServerErrorProps } from './types';

import { RSpinner, RBtn } from '@/ui/atoms';

@Options({
  components: {
    RBtn,
    RSpinner,
  },
})
export default class ModalServerError extends Vue implements ModalServerErrorProps {
  @Prop(String) reloadUrl!: string;

  reloadRoute(): void {
    if (this.reloadUrl) {
      this.$router.push(this.reloadUrl);
    } else {
      this.$router.go(0);
    }
  }
}
