import {
  onMounted, computed, watch, ref, onBeforeUnmount, onBeforeMount,
} from 'vue';
import { registerModule, useModule } from 'vuex-simple';

import { startPage } from '@/common';

import { PagedDataComposition } from './types';
import { AsyncCbWrapper } from '@/store/stores/pagedData/types';

import { useScroll } from '@/composable/scroll';

import vuexSimpleStore, { DynamicStoresNamespaces } from '@/store';
import PagedDataStore from '@/store/stores/pagedData';

export function usePagedData<T>(namespace: DynamicStoresNamespaces, asyncCb: AsyncCbWrapper<T>): PagedDataComposition<T> {
  const page = ref(startPage);
  const scroll = useScroll();

  const pagedDataStore = ref<PagedDataStore<T> | undefined>();

  const allPages = computed(
    () => pagedDataStore.value?.allPages
  );
  const isSomePageLoading = computed(
    () => pagedDataStore.value?.isSomePageLoading
  );
  const isFirstPageLoading = computed(
    () => pagedDataStore.value?.isFirstPageLoading
  );

  function getPagedDataStore(): PagedDataStore<T> | undefined {
    return useModule<PagedDataStore<T>>(vuexSimpleStore, [ namespace ]);
  }

  function getPagedData(page: number): void {
    const defaultCount = 10;

    if (!pagedDataStore.value) {
      return;
    }

    pagedDataStore.value.getPagedData({
      page,
      asyncCb: asyncCb(page, defaultCount),
    });
  }

  async function tryGetNextPagedData(): Promise<void> {
    if (!pagedDataStore.value) {
      return;
    }

    const nextPage = page.value + 1;

    await getPagedData(nextPage);

    if (!pagedDataStore.value.state.data[page.value]?.error) {
      page.value = nextPage;
    }
  }

  function registerPagedDataStoreIfNone(): PagedDataStore<T> | undefined {
    if (!getPagedDataStore()) {
      registerModule(vuexSimpleStore, [ namespace ], new PagedDataStore());
    }

    return getPagedDataStore();
  }

  onMounted(() => {
    getPagedData(page.value);
  });

  onBeforeMount(() => {
    pagedDataStore.value = registerPagedDataStoreIfNone();
  });

  onBeforeUnmount(() => {
    page.value = startPage;

    if (pagedDataStore.value) {
      pagedDataStore.value.clear();
    }
  });

  watch(
    scroll.isScrolledToBottom,
    (isScrolledToBottom: boolean) => {
      if (isSomePageLoading.value || !isScrolledToBottom) {
        return;
      }

      tryGetNextPagedData();
    }
  );

  return {
    pagedDataPages: pagedDataStore.value?.state,
    allPages,
    isSomePageLoading,
    isFirstPageLoading,
  };
}
