import { AxiosRequestConfig } from 'axios';
import { CrudService, BaseService } from '@/api';

import { FileParams } from './types';

export class FilesService<T, D> extends CrudService<T, D> {
  constructor() {
    super('/Files');
  }

  public async getFileById(config?: AxiosRequestConfig<FileParams>): Promise<Blob> {
    const response = await BaseService.axios.get(`${this.path}/files/${config?.params.fileId}`, {
      responseType: 'blob',
    });

    return response.data;
  }
}

export default new FilesService<unknown, unknown>();
