import { AxiosRequestConfig } from 'axios';
import { CrudService } from '@/api';

import { SpecialityDto } from '@/types/models';

export class SpecialitiesService<T, D> extends CrudService<T, D> {
  constructor() {
    super('/Specialities');
  }

  public async getSpecialities(config?: AxiosRequestConfig<D>): Promise<T[]> {
    return this.read(config) as Promise<T[]>;
  }
}

export default new SpecialitiesService<SpecialityDto, unknown>();
