
import { createVNode } from 'vue';
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import identity from 'lodash/fp/identity';

import { TypographyColor, TypographyVariants, TypographyProps } from './types';

@Options({})
export default class Typography extends Vue implements TypographyProps {
  @Prop({ default: 'p' }) readonly tag!: string;

  @Prop({ default: 'body-1-regular' }) readonly variant!: TypographyVariants;

  @Prop({ default: 'base-color' }) readonly color!: TypographyColor;

  // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
  render() {
    const defaultSlot = this.$slots.default ?? identity;

    return createVNode(
      this.tag,
      {
        class: [
          this.variant,
          this.color,
          'text',
        ],
      },
      defaultSlot()
    );
  }
}
