
import { Options, Vue } from 'vue-class-component';
import { Prop, Watch } from 'vue-property-decorator';

import { getBase64 } from '@/utils';

import { PdfViewerContent, PrfViewerProps } from './types';

import { TextRows } from '@/ui/organisms';

@Options({
  components: {
    TextRows,
  },
})
export default class PdfViewer extends Vue implements PrfViewerProps {
  @Prop(Boolean) loading!: boolean;

  @Prop({ type: String, default: '100vh' }) height!: string;

  @Prop({ type: String, default: '100%' }) width!: string;

  @Prop([ String, null ]) error!: string | null;

  @Prop([ Blob, null ]) content!: PdfViewerContent;

  base64Content: string | null = null;

  convertingErrorMessage: string | null = null;

  get errorMessage(): string | null {
    return this.error || this.convertingErrorMessage;
  }

  @Watch('content')
  async onContentChange(content: PdfViewerContent): Promise<void> {
    if (content) {
      try {
        this.base64Content = await getBase64(content);
      } catch (error: any) {
        this.convertingErrorMessage = error.message;
      }
    }
  }
}
