import { Notify } from 'quasar';

import { NotificationOpts } from './types';

export const defaultOpts = (): Exclude<NotificationOpts, string> => ({
  classes: 'r-notification',
  icon: '',
  position: 'top',
  timeout: 2500,
});

export function notifyError(opts: NotificationOpts): void {
  Notify.create(
    typeof opts === 'string'
      ? opts
      : {
        type: 'negative',
        ...defaultOpts(),
        ...opts,
      }
  );
}
