import { App } from 'vue';

import {
  isTodayDate,
  isTomorrowDate,
  formatDate,
  getFullName,
  formatHours,
  formatMinutes,
  formatUtcHours,
  formatUtсMinutes,
  formatDayMonth,
  formatHoursMinutes,
  formatPhoneNumber,
  formatTime,
  isValidBirthDate,
  isOfAge,
} from '@/utils';

export const dateFilters = {
  isTodayDate,
  isTomorrowDate,
  formatDate,
  formatHours,
  formatMinutes,
  formatUtcHours,
  formatUtсMinutes,
  formatDayMonth,
  formatHoursMinutes,
  formatTime,
  isValidBirthDate,
  isOfAge,
};

export const stringFilters = {
  getFullName,
  formatPhoneNumber,
};

export function initFilters(app: App<Element>): App<Element> {
  const clonedApp = { ...app };

  clonedApp.config.globalProperties.$dateFilters = dateFilters;

  clonedApp.config.globalProperties.$stringFilters = stringFilters;

  return clonedApp;
}
