
import { Options, Vue } from 'vue-class-component';
import { Model, Prop } from 'vue-property-decorator';

import { RInputProps } from './types';

@Options({})
export default class RInput extends Vue implements RInputProps {
  @Model('input', { type: String }) modelValue!: string;

  @Prop() readonly label!: string;

  @Prop() readonly hint!: string;

  @Prop(Boolean) readonly hideHint!: boolean;

  @Prop() readonly labelColor!: string;

  @Prop() readonly color!: string;

  @Prop(Boolean) readonly borderless!: boolean;

  @Prop(Boolean) readonly disable!: boolean;

  @Prop(Boolean) readonly readonly!: boolean;

  @Prop(Boolean) readonly autofocus!: boolean;

  @Prop(Boolean) readonly rClearable!: boolean;

  @Prop(Boolean) readonly error!: boolean;

  @Prop(Boolean) readonly noErrorIcon!: boolean;

  @Prop() readonly mask!: string;

  @Prop({ type: [ Boolean, String ], default: false }) readonly fillMask!: boolean | string;

  get value(): string {
    return this.modelValue;
  }

  set value(value: string) {
    this.$emit('update:modelValue', value);
  }

  clear(): void {
    this.$emit('update:modelValue', '');
  }

  get canBeCleared(): boolean {
    return this.rClearable && this.modelValue.length > 0;
  }
}
