/* eslint-disable @typescript-eslint/camelcase */
import { onMounted, computed } from 'vue';
import { useRoute, useRouter } from 'vue-router';

import { useSimpleStore } from '@/utils/vuex';
import {
  getDoctorFromBrowserStorage, getSpecialityFromBrowserStorage, notifyError, pushEvent,
} from '@/utils';

import {
  CreateOrderComposition,
  OrderCheckComposition,
  OrderCheckStatus,
  ConsultationByOrderComposition,
} from './types';
import { RouteNames, RoutePaths } from '@/router/types';

export function useCreateOrder(): CreateOrderComposition {
  const store = useSimpleStore();

  async function addNewOrder(): Promise<void> {
    const { data } = store.confirmationInfo.state;

    if (!data) {
      notifyError({
        message: 'Не удалось найти данные для создания заказа',
      });
      return;
    }

    await store.orders.addNewOrder({
      productId: data.product.productId,
      intervalId: data.interval.intervalId,
      specialityId: data.speciality.specialityId,
      doctorId: data.doctor.doctorId,
      returnUri: `${window.location.origin}${RoutePaths.INFO_AFTER_PAYMENT}`,
    });

    if (store.orders.state.paymentInfo.orderId) {
      window.location.replace(store.orders.state.paymentInfo.paymentUri);
    }
  }

  return {
    info: store.orders.state,
    addNewOrder,
  };
}

export function useOrderCheck(): OrderCheckComposition {
  const store = useSimpleStore();

  const isPaymentFailed = computed(() => store.orders.isPaymentFailed);
  const isConsultationCreateFailed = computed(() => store.orders.isConsultationCreateFailed);

  async function getOrderStatus(orderId: string): Promise<OrderCheckStatus> {
    await store.orders.getOrderStatus({
      orderId,
    });

    if (isPaymentFailed.value) {
      return 'PaymentFail';
    }

    await store.orders.checkConsultationCreated({
      orderId,
    });

    if (isConsultationCreateFailed.value) {
      return 'CreationFail';
    }

    return 'Success';
  }

  return {
    getOrderStatus,
  };
}

export function useOrderConsultationIfOrder(): ConsultationByOrderComposition {
  const store = useSimpleStore();
  const route = useRoute();
  const router = useRouter();

  const orderCheck = useOrderCheck();

  const orderId = route.query.orderId as string;
  const isConsultationProcessing = computed(() => store.orders.isConsultationProcessing);
  const isConsultationCreateFailed = computed(() => store.orders.isConsultationCreateFailed);

  function showErrorPage(description: string, title?: string): void {
    router.push({
      name: RouteNames.DETAILED_ERROR,
      query: {
        title,
        description,
      },
    });
  }

  async function getConsultationByOrderId(): Promise<void> {
    store.consultation.clear();

    return store.consultation.getConsultationByOrderId({
      orderId,
    });
  }

  onMounted(async () => {
    const orderStatus = await orderCheck.getOrderStatus(orderId);

    if (orderStatus === 'PaymentFail') {
      showErrorPage(
        `Мы не смогли списать деньги с карты.
        Оплата не была произведена.
        Попробуйте повторить попытку.`,
        'Деньги не были списаны.'
      );
    }

    if (orderStatus === 'CreationFail') {
      showErrorPage(
        `Мы получили деньги, но не смогли записать вас к врачу.
        Если в течение часа консультация не появится в разделе "События",
        пожалуйста, обратитесь в службу поддержки.`
      );
    }

    if (orderStatus === 'Success') {
      await getConsultationByOrderId();

      pushEvent('scheduled_confirm_done', {
        consultation_guid: store.consultation.state.data?.id ?? '',
        doctor_guid: getDoctorFromBrowserStorage()?.id ?? '',
        eventCategory: 'telemed',
        eventLabel: getSpecialityFromBrowserStorage()?.specialityName ?? '',
      });
    }
  });

  return {
    consultation: store.consultation.state,
    isConsultationProcessing,
    isConsultationCreateFailed,
  };
}
