import {
  State, Mutation, Action, Getter,
} from 'vuex-simple';

import {
  ConsultationDto, DoctorCommonDto, DoctorIntervalValueDto, SpecialityDto,
} from '@/types/models';
import { ConsultationInfoModel } from './types';

import {
  getConsultationFromBrowserStorage,
  getDoctorFromBrowserStorage,
  getFullName,
  getIntervalFromBrowserStorage,
  getSpecialityFromBrowserStorage,
  setConsultationToBrowserStorage,
  setDoctorToBrowserStorage,
  setIntervalToBrowserStorage,
  setSpecialityToBrowserStorage,
} from '@/utils';

class ConsultationInfoStore implements ConsultationInfoModel {
  @State()
  consultation: ConsultationDto | null = null;

  @State()
  doctor: DoctorCommonDto | null = null;

  @State()
  interval: DoctorIntervalValueDto | null = null;

  @State()
  speciality: SpecialityDto | null = null;

  @Getter()
  get doctorFullName(): string {
    return getFullName(this.doctor);
  }

  @Getter()
  get specialityName(): string {
    return this.speciality?.specialityName ?? '';
  }

  @Getter()
  get doctorNameWithSpeciality(): string {
    return `${this.specialityName} ${this.doctorFullName}`.trim();
  }

  @Getter()
  get canConfirmConsultation(): boolean {
    const { speciality, interval } = this;

    return !!speciality && !!interval;
  }

  @Mutation()
  setConsultation(consultation: ConsultationDto | null): void {
    this.consultation = consultation;
  }

  @Mutation()
  setSpeciality(speciality: SpecialityDto | null): void {
    this.speciality = speciality;
  }

  @Mutation()
  setInterval(interval: DoctorIntervalValueDto | null): void {
    this.interval = interval;
  }

  @Mutation()
  setDoctor(doctor: DoctorCommonDto | null): void {
    this.doctor = doctor;
  }

  @Action()
  tryLoadConsultationInfoFromBrowserStorage(): void {
    this.saveConsultationToStoreAndBrowserStorage(
      getConsultationFromBrowserStorage()
    );
    this.saveSpecialityToStoreAndBrowserStorage(
      getSpecialityFromBrowserStorage()
    );
    this.saveIntervalToStoreAndBrowserStorage(
      getIntervalFromBrowserStorage()
    );
    this.saveDoctorToStoreAndBrowserStorage(
      getDoctorFromBrowserStorage()
    );
  }

  @Action()
  saveConsultationToStoreAndBrowserStorage(consultation: ConsultationDto | null): void {
    this.setConsultation(consultation);
    setConsultationToBrowserStorage(consultation);
  }

  @Action()
  saveSpecialityToStoreAndBrowserStorage(speciality: SpecialityDto | null): void {
    this.setSpeciality(speciality);
    setSpecialityToBrowserStorage(speciality);
  }

  @Action()
  saveIntervalToStoreAndBrowserStorage(interval: DoctorIntervalValueDto | null): void {
    this.setInterval(interval);
    setIntervalToBrowserStorage(interval);
  }

  @Action()
  saveDoctorToStoreAndBrowserStorage(doctor: DoctorCommonDto | null): void {
    this.setDoctor(doctor);
    setDoctorToBrowserStorage(doctor);
  }
}

export default ConsultationInfoStore;
