import {
  State, Mutation, Action, Getter,
} from 'vuex-simple';

import {
  UserModel, UserState, NeedToUpdateProfileState,
} from './types';
import { UserDataDto, UserDataResponse } from '@/types/models';

import UserServiceInstance from '@/services/user';

class UserStore implements UserModel {
  @State()
  state: UserState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @State()
  needToUpdateProfile: NeedToUpdateProfileState = {
    needToUpdateProfile: true,
    error: '',
    isLoading: false,
  }

  @Getter()
  get isNewUser(): boolean {
    return this.needToUpdateProfile.needToUpdateProfile;
  }

  @Mutation()
  setUserDataIsLoading(isLoading: boolean): void {
    this.state.isLoading = isLoading;
  }

  @Mutation()
  setUserDataError(error: string): void {
    this.state.error = error;
  }

  @Mutation()
  setUserData(data: UserDataResponse | null): void {
    this.state.data = data;
  }

  @Mutation()
  setNeedToUpdateProfileIsLoading(isLoading: boolean): void {
    this.needToUpdateProfile.isLoading = isLoading;
  }

  @Mutation()
  setNeedToUpdateProfileError(error: string): void {
    this.needToUpdateProfile.error = error;
  }

  @Mutation()
  setNeedToUpdateProfileData(data: boolean): void {
    this.needToUpdateProfile.needToUpdateProfile = data;
  }

  @Action()
  updateUserData = async (userData: UserDataDto): Promise<void> => {
    this.setUserDataIsLoading(true);
    this.setUserDataError('');
    try {
      const data = await UserServiceInstance.updateUser(userData);

      this.setUserData(data);
    } catch (error: any) {
      this.setUserDataError(error.message);
    } finally {
      this.setUserDataIsLoading(false);
    }
  }

  @Action()
  getNeedToUpdateProfile = async (): Promise<void> => {
    this.setNeedToUpdateProfileIsLoading(true);
    this.setNeedToUpdateProfileError('');
    try {
      const needToUpdateProfile = await UserServiceInstance.getNeedToUpdateProfile();

      this.setNeedToUpdateProfileData(needToUpdateProfile);
    } catch (error: any) {
      this.setNeedToUpdateProfileError(error.message);
    } finally {
      this.setNeedToUpdateProfileIsLoading(false);
    }
  }

  @Action()
  clear = async (): Promise<void> => {
    this.setUserData(null);
  }
}

export default UserStore;
