import moment, { MomentInput } from 'moment';

export function isTodayDate(dateString: MomentInput): boolean {
  return moment().isSame(dateString, 'year')
    && moment().isSame(dateString, 'month')
    && moment().isSame(dateString, 'day');
}

export function isTomorrowDate(dateString: MomentInput): boolean {
  const tomorrowFromToday = moment().add(1, 'day');

  return tomorrowFromToday.isSame(dateString, 'year')
    && tomorrowFromToday.isSame(dateString, 'month')
    && tomorrowFromToday.isSame(dateString, 'day');
}

export function formatDate(dateString: MomentInput, format = 'DD.MM.YYYY'): string {
  return moment(dateString).format(format);
}

export function formatTime(date: string): string {
  return moment(date).format('HH:mm');
}

export function formatHours(dateString: MomentInput): string {
  return moment(dateString).format('HH');
}

export function formatMinutes(dateString: MomentInput): string {
  return moment(dateString).format('mm');
}

export function formatUtcHours(dateString: MomentInput): string {
  return moment(dateString).utc().format('HH');
}

export function formatUtсMinutes(dateString: MomentInput): string {
  return moment(dateString).utc().format('mm');
}

export function formatDayMonth(dateString: MomentInput): string {
  return moment(dateString).format('D MMMM');
}

export function formatHoursMinutes(dateString: MomentInput): string {
  return moment(dateString).format('HH:mm');
}

export function getAdultAgeInMs(): number {
  return moment.duration(18, 'years').asMilliseconds();
}

export function isOfAge(dateString: MomentInput, format = 'DD.MM.YYYY'): boolean {
  return moment().diff(moment(dateString, format)) >= getAdultAgeInMs();
}
