import { AxiosError, AxiosResponse } from 'axios';

import { ErrorCancel, HttpStatus, RequestErrorMessage } from '@/types/common';
import { ErrorResponse } from '@/types/models';
import { AxiosErrorTypes } from './types';

export function delayPromise(ms: number): Promise<unknown> {
  return new Promise((resolve => {
    setTimeout(resolve, ms);
  }));
}

export function getPassedTimestamp(dateStart: number): number {
  return Date.now() - dateStart;
}

/**
 * @param dateStart timestamp начала отсчета
 * @param timeout timestamp, если === 0, isTimeout всегда возвратит false,
 * нужны последующие проверки результатов исполнения
 */
export const hasTimeout = (dateStart: number, timeout: number): boolean => (
  timeout > 0 && getPassedTimestamp(dateStart) > timeout
);

export function isErrorResponse<T>(response: AxiosResponse<T> | AxiosError<AxiosErrorTypes>): response is AxiosError<ErrorResponse> {
  return !!(response as AxiosError).response?.data?.error;
}

export function isRequestError<T>(response: AxiosResponse<T> | AxiosError<AxiosErrorTypes>): response is AxiosError<Error> {
  return (response as any)?.status !== HttpStatus.SUCCESS;
}

export function isRequestAborted<T>(response: AxiosResponse<T> | AxiosError<AxiosErrorTypes> | ErrorCancel): response is ErrorCancel {
  return (response as any)?.message === RequestErrorMessage.CANCELED;
}
