import {
  State, Mutation, Action, Getter,
} from 'vuex-simple';
import flatMap from 'lodash/flatMap';
import keys from 'lodash/keys';
import some from 'lodash/some';
import compact from 'lodash/compact';

import { startPage } from '@/common';

import {
  PagedDataPagesModel, PagedDataPagesState, PagedDataPages, GetPagedDataPayload,
} from './types';

class PagedDataStore<T> implements PagedDataPagesModel<T> {
  @State()
  state: PagedDataPagesState<T> = {
    data: {},
  }

  @Getter()
  get allPages(): T[] {
    return compact(flatMap(this.state.data, 'data'));
  }

  @Getter()
  get hasPages(): boolean {
    return !!keys(this.state.data).length;
  }

  @Getter()
  get isSomePageLoading(): boolean {
    return some(this.state.data, value => value.isLoading);
  }

  @Getter()
  get isFirstPageLoading(): boolean {
    return !!this.state.data[startPage]?.isLoading;
  }

  @Mutation()
  setPagedDataPageIsLoading({ page, isLoading }: { page: number; isLoading: boolean }): void {
    this.state.data[page].isLoading = isLoading;
  }

  @Mutation()
  setPagedDataPageError({ page, error }: { page: number; error: string }): void {
    this.state.data[page].error = error;
  }

  @Mutation()
  setPagedDataPageData({ page, data }: { page: number; data: T[] }): void {
    this.state.data[page].data = data;
  }

  @Mutation()
  setPagedDataPagesData(data: PagedDataPages<T>): void {
    this.state.data = data;
  }

  @Action()
  getPagedData = async (payload: GetPagedDataPayload<T>): Promise<void> => {
    const { page, asyncCb } = payload;

    this.state.data[page] = {
      ...(this.state.data[page] ?? {}),
    };

    this.setPagedDataPageIsLoading({
      page,
      isLoading: true,
    });
    this.setPagedDataPageError({
      page,
      error: '',
    });

    try {
      const { items } = await asyncCb();

      this.setPagedDataPageData({
        page,
        data: items as any[],
      });
    } catch (error: any) {
      this.setPagedDataPageError({
        page,
        error: error.message,
      });
    } finally {
      this.setPagedDataPageIsLoading({
        page,
        isLoading: false,
      });
    }
  }

  @Action()
  clear = (): void => {
    this.setPagedDataPagesData({});
  }
}

export default PagedDataStore;
