import { NavigationGuard } from './types';
import { Store } from '@/store';
import GuestGuard from './GuestGuard';
import { UserProfile } from '@/types/models';

import AbstractGuard from './AbstractGuard';
import UserGuard from './UserGuard';

export default class NavigationGuardFactory {
  store: Store;

  guard: NavigationGuard;

  constructor(store: Store, guard: NavigationGuard) {
    this.store = store;
    this.guard = guard;
  }

  createGuard(type: UserProfile | undefined): AbstractGuard {
    if (type === 'ClientGuestProfile') {
      return new GuestGuard(this.store, this.guard);
    }

    if (type === 'ClientProfile') {
      return new UserGuard(this.store, this.guard);
    }

    return new GuestGuard(this.store, this.guard);
  }
}
