import DoctorBaseDto from './DoctorBaseDto';
import DocumentDto from './DocumentDto';
import SpecialityDto from './SpecialityDto';

export const enum ConsultationStatus {
  New = 'New',
  InProgress = 'InProgress',
  CanceledByClient = 'CanceledByClient',
  CanceledByOperator = 'CanceledByOperator',
  TransferredByClient = 'TransferredByClient',
  TransferredByOperator = 'TransferredByOperator',
  Completed = 'Completed',
}

export class ConsultationDto {
  // eslint-disable-next-line no-useless-constructor
  constructor(
    public id: string | null,
    public status: ConsultationStatus,
    public creationDate: string,
    public calculatedDateStart: string,
    public doctor: DoctorBaseDto,
    public speciality: SpecialityDto,
    public documents: DocumentDto[],
    public conclusionId: string,
    public conclusionExists: string
  ) {}
}
