import {
  State, Mutation, Action,
} from 'vuex-simple';
import moment from 'moment';

import {
  AuthorizationConfirmationState,
  AuthorizationModel,
  AuthorizationState,
  RemainingTimeSeconds,
} from './types';

import {
  SmsLoginResponse, SmsLoginRequest, SmsConfirmResponse, SmsConfirmRequest,
} from '@/services';
import AuthorizationServiceInstance from '@/services/authorization';

import { ErrorCode, UnwrappedErrorResponse } from '@/types/models';

import { Store } from '@/store';

import {
  isErrorResponse, isRequestAborted, isRequestError,
} from '@/utils';

class AuthorizationStore implements AuthorizationModel {
  // eslint-disable-next-line no-useless-constructor
  constructor(private root: Store) {}

  @State()
  smsSending: AuthorizationState = {
    data: null,
    error: null,
    isLoading: false,
  }

  @State()
  smsConfirmation: AuthorizationConfirmationState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @State()
  remainingTimeSeconds: RemainingTimeSeconds = null;

  @Mutation()
  setSmsSendingIsLoading(isLoading: boolean): void {
    this.smsSending.isLoading = isLoading;
  }

  @Mutation()
  setSmsSendingError(error: UnwrappedErrorResponse | null): void {
    this.smsSending.error = error;
  }

  @Mutation()
  setSmsSendingData(data: SmsLoginResponse | null): void {
    this.smsSending.data = data;
  }

  @Mutation()
  setRemainingTimeSeconds(remainingTimeSeconds: number): void {
    this.remainingTimeSeconds = remainingTimeSeconds;
  }

  @Mutation()
  setSmsConfirmationIsLoading(isLoading: boolean): void {
    this.smsConfirmation.isLoading = isLoading;
  }

  @Mutation()
  setSmsConfirmationError(error: string | null): void {
    this.smsConfirmation.error = error;
  }

  @Mutation()
  setSmsConfirmationData(data: SmsConfirmResponse | null): void {
    this.smsConfirmation.data = data;
  }

  @Action()
  sendSms = async (payload: SmsLoginRequest): Promise<void> => {
    this.setSmsSendingIsLoading(true);
    this.setSmsSendingError(null);
    this.root.modals.updateServerErrorModalShow({
      show: false,
    });

    const authorizationResponse = await AuthorizationServiceInstance.sendSms(payload);

    if (isRequestAborted(authorizationResponse)) {
      this.setSmsSendingIsLoading(false);
      return;
    }

    if (isErrorResponse(authorizationResponse)) {
      this.setSmsSendingIsLoading(false);

      const errorResponse = authorizationResponse.response?.data.error;

      if (errorResponse?.code === ErrorCode.TooEarlyForSendCode) {
        this.setSmsSendingError(errorResponse);
        this.setRemainingTimeSeconds(errorResponse.arguments.remainigTimeSeconds);
      }
      return;
    }

    if (isRequestError(authorizationResponse)) {
      this.root.modals.updateServerErrorModalShow({
        show: true,
      });
      return;
    }

    this.setSmsSendingData(authorizationResponse.data);
    this.setRemainingTimeSeconds(authorizationResponse.data.earliestTimeNextSendingSeconds);
  }

  @Action()
  confirmSms = async (requestParams: SmsConfirmRequest): Promise<void> => {
    this.setSmsConfirmationIsLoading(true);
    this.setSmsConfirmationError(null);
    try {
      const authorizationTokens = await AuthorizationServiceInstance.confirmSms(requestParams);

      this.root.authentication.saveTokensToStoreAndBrowserStorage(authorizationTokens);
      this.root.authentication.setSmsConfirmUpdateDate(moment());
      this.setSmsConfirmationData(authorizationTokens);
    } catch (error: any) {
      this.setSmsConfirmationError(error.message);
    } finally {
      this.setSmsConfirmationIsLoading(false);
    }
  }

  @Action()
  decreaseRemainingTime = (): void => {
    if (this.remainingTimeSeconds !== null) {
      this.setRemainingTimeSeconds(this.remainingTimeSeconds - 1);
    }
  }

  @Action()
  clear = async (): Promise<void> => {
    this.setSmsSendingData(null);

    this.setSmsSendingError(null);
    this.setSmsConfirmationError(null);
  }
}

export default AuthorizationStore;
