import { NavigationGuard } from './types';
import { Permissions } from '@/types/common';
import { RouteNames } from '@/router/types';
import { Store } from '@/store';

import AbstractGuard from './AbstractGuard';

export default class GuestGuard extends AbstractGuard {
  store: Store;

  guard: NavigationGuard;

  constructor(store: Store, guard: NavigationGuard) {
    super();

    this.store = store;
    this.guard = guard;
  }

  async nextName(): Promise<RouteNames> {
    const { decodedToken } = this.store.authentication;

    const isGuestRoleOrUnknown = !decodedToken?.UserProfile || decodedToken.UserProfile === 'ClientGuestProfile';
    const nextIsGuest = this.hasPermissions([ Permissions.GUEST ]);

    if (isGuestRoleOrUnknown && !nextIsGuest) {
      return RouteNames.AUTHORIZATION;
    }

    if (nextIsGuest) {
      await this.store.authentication.loadGuestTokens();
    }

    if (!this.isAuthenticated) {
      return RouteNames.NOT_FOUND;
    }

    return this.guard.to.name as RouteNames;
  }
}
