
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { RImgProps, QImgFit } from './types';

import { Dictionary } from '@/utils';

@Options({})
export default class RImg extends Vue implements RImgProps {
  @Prop({ default: '' }) readonly src!: string;

  @Prop({ default: '64px' }) readonly width!: string;

  @Prop({ default: '64px' }) readonly height!: string;

  @Prop({ default: 'contain' }) readonly fit!: QImgFit;

  @Prop({ default: 'Изображание не найдено.' }) readonly alt!: string;

  @Prop({ default: false }) readonly rounded!: boolean;

  @Prop({ default: null }) readonly imgStyle!: Dictionary | undefined;

  // todo color
  @Prop({ default: 'blue' }) readonly spinnerColor!: string;

  @Prop({ default: '30px' }) readonly spinnerSize!: string;
}
