import {
  State, Mutation, Action, Getter,
} from 'vuex-simple';

import { poll } from '@/utils';

import {
  PaymentInfoResponse, ConsultationCreatedResponse, OrderDto, OrderStatus,
} from '@/types/models';
import {
  OrdersModel, OrdersState, ConsultationCheckState, OrderStatusState,
} from './types';
import {
  ConsultationCreatedParams, OrderStatusParams, PaymentInfoRequest,
} from '@/services/orders/types';

import OrdersServiceInstance from '@/services/orders';

class OrdersStore implements OrdersModel {
  @State()
  state: OrdersState = {
    paymentInfo: {
      orderId: '',
      paymentUri: '',
    },
    error: '',
    isLoading: false,
  }

  @Getter()
  get isConsultationProcessing(): boolean {
    return this.consultationCheck.isLoading || this.orderStatus.isLoading;
  }

  @Getter()
  get isPaymentFailed(): boolean {
    const orderFailStatuses = [
      OrderStatus.PaymentFail,
      OrderStatus.PaymentCreationFail,
    ];

    return !!this.orderStatus.error
      || orderFailStatuses.includes(this.orderStatus.data?.status as OrderStatus);
  }

  @Getter()
  get isConsultationCreateFailed(): boolean {
    return !!this.consultationCheck.error || !this.consultationCheck.data;
  }

  @State()
  consultationCheck: ConsultationCheckState = {
    data: false,
    error: '',
    isLoading: false,
  }

  @State()
  orderStatus: OrderStatusState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @Mutation()
  setOrdersIsLoading(isLoading: boolean): void {
    this.state.isLoading = isLoading;
  }

  @Mutation()
  setOrdersError(error: string): void {
    this.state.error = error;
  }

  @Mutation()
  setPaymentInfo(data: PaymentInfoResponse): void {
    this.state.paymentInfo = data;
  }

  @Mutation()
  setConsultationCheckIsLoading(isLoading: boolean): void {
    this.consultationCheck.isLoading = isLoading;
  }

  @Mutation()
  setConsultationCheckError(error: string): void {
    this.consultationCheck.error = error;
  }

  @Mutation()
  setConsultationCheckData(data: boolean): void {
    this.consultationCheck.data = data;
  }

  @Mutation()
  setOrderStatusData(data: OrderDto | null): void {
    this.orderStatus.data = data;
  }

  @Mutation()
  setOrderStatusError(error: string): void {
    this.orderStatus.error = error;
  }

  @Mutation()
  setOrderStatusIsLoading(isLoading: boolean): void {
    this.orderStatus.isLoading = isLoading;
  }

  @Action()
  addNewOrder = async (data: PaymentInfoRequest): Promise<void> => {
    this.setOrdersIsLoading(true);
    this.setOrdersError('');
    try {
      const paymentInfo = await OrdersServiceInstance.createOrderWithPayment(data);

      this.setPaymentInfo(paymentInfo);
    } catch (error: any) {
      this.setOrdersError(error.message);
    } finally {
      this.setOrdersIsLoading(false);
    }
  }

  @Action()
  checkConsultationCreated = async (params: ConsultationCreatedParams): Promise<void> => {
    this.setConsultationCheckIsLoading(true);
    this.setConsultationCheckError('');
    try {
      const oneMinuteTimeout = 60000;
      const tenSecondsInterval = 10000;
      const { consultationWasCreated } = await poll<ConsultationCreatedResponse>({
        requestCb: () => OrdersServiceInstance.checkConsultationCreated({ params }),
        predicate: response => response.consultationWasCreated,
        interval: tenSecondsInterval,
        timeout: oneMinuteTimeout,
      });

      this.setConsultationCheckData(consultationWasCreated);
    } catch (error: any) {
      this.setConsultationCheckError(error.message);
    } finally {
      this.setConsultationCheckIsLoading(false);
    }
  }

  @Action()
  getOrderStatus = async (params: OrderStatusParams): Promise<void> => {
    this.setOrderStatusIsLoading(true);
    this.setOrderStatusError('');
    try {
      const oneMinuteTimeout = 60000;
      const tenSecondsInterval = 10000;
      const orderStatus = await poll<OrderDto>({
        requestCb: () => OrdersServiceInstance.getOrderStatus({ params }),
        predicate: response => {
          const orderDoneStatuses = [
            OrderStatus.PaymentFail,
            OrderStatus.PaymentCreationFail,
            OrderStatus.Complete,
          ];

          return orderDoneStatuses.includes(response.status);
        },
        interval: tenSecondsInterval,
        timeout: oneMinuteTimeout,
      });

      this.setOrderStatusData(orderStatus);
    } catch (error: any) {
      this.setOrderStatusError(error.message);
    } finally {
      this.setOrderStatusIsLoading(false);
    }
  }

  @Action()
  clearOrderStatus = async (): Promise<void> => {
    this.setOrderStatusData(null);
  }
}

export default OrdersStore;
