
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { RBtnProps, RBtnTo } from './types';

@Options({})
export default class RBtn extends Vue implements RBtnProps {
  @Prop(String) readonly label!: string;

  @Prop(Boolean) readonly flat!: boolean;

  @Prop(Boolean) readonly disable!: boolean;

  @Prop(Boolean) readonly outline!: boolean;

  @Prop(Boolean) readonly loading!: boolean;

  @Prop({ default: 'primary' }) readonly color!: string;

  @Prop({ default: '' }) readonly textColor!: string;

  @Prop({ type: Boolean, default: true }) readonly unelevated!: boolean;

  @Prop(Boolean) readonly round!: boolean;

  @Prop(Boolean) readonly rRounded!: boolean;

  @Prop(String) readonly padding!: string;

  @Prop(Boolean) readonly fullWidth!: boolean;

  @Prop([ Object, String ]) readonly to!: RBtnTo;

  @Prop(Boolean) readonly rFab!: boolean;
}
