
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import fill from 'lodash/fill';

import { RatingScoreProps } from './types';

import { RSpinner } from '@/ui/atoms';

@Options({
  components: {
    RSpinner,
  },
})
export default class RatingScore extends Vue implements RatingScoreProps {
  @Prop(Number) score!: number;

  @Prop({ type: Number, default: 5 }) totalScore!: number;

  get scores(): Array<number | null> {
    const totalScores = new Array(this.totalScore);
    const filledTotalScores = fill(totalScores, null).map((_, score) => score);

    return fill([ ...filledTotalScores ], null, this.score, 5);
  }
}
