import { ref, onMounted, onBeforeUnmount } from 'vue';

import { ScrollComposition } from './types';

export function useScroll(): ScrollComposition {
  const isScrolledToBottom = ref(false);

  function getYScrollWithHeight(): number {
    return Math.round(window.pageYOffset + document.documentElement.clientHeight);
  }

  function handleScroll(): void {
    isScrolledToBottom.value = getYScrollWithHeight() === document.documentElement.scrollHeight;
  }

  onMounted(() => {
    window.addEventListener('scroll', handleScroll);
  });

  onBeforeUnmount(() => {
    window.removeEventListener('scroll', handleScroll);
  });

  return {
    isScrolledToBottom,
  };
}
