import { NavigationGuard } from './types';
import { Permissions } from '@/types/common';
import { RouteNames } from '@/router/types';
import { Store } from '@/store';

import AbstractGuard from './AbstractGuard';

export default class UserGuard extends AbstractGuard {
  store: Store;

  guard: NavigationGuard;

  constructor(store: Store, guard: NavigationGuard) {
    super();

    this.store = store;
    this.guard = guard;
  }

  async nextName(): Promise<RouteNames> {
    if (this.isAuthenticated) {
      return this.guard.to.name as RouteNames;
    }

    const nextIsUser = this.hasPermissions([ Permissions.CODE_AUTHORIZED_USER ]);

    if (this.guard.to.name !== RouteNames.AUTHORIZATION && nextIsUser) {
      await this.store.authentication.loadUserTokens();
    }

    if (!this.isAuthenticated) {
      return RouteNames.AUTHORIZATION;
    }

    return this.guard.to.name as RouteNames;
  }
}
