import {
  onMounted, computed, onUnmounted, ref,
} from 'vue';
import { useRoute } from 'vue-router';
import moment, { MomentInput } from 'moment';

import { useSimpleStore } from '@/utils/vuex';
import { notifyError } from '@/utils';

import {
  DoctorAppointmentComposition,
  AppointmentIntervalsComposition,
  DoctorExperienceComposition,
} from './types';
import { DoctorIntervalDto } from '@/types/models';

import { useCheckUserProfile } from '@/composable/user';

export function useDoctorAppointment(): DoctorAppointmentComposition {
  const store = useSimpleStore();
  const route = useRoute();

  onMounted(() => {
    store.doctorAppointment.getDoctor({
      doctorId: route.params.doctorId as string,
    });
  });

  onUnmounted(() => {
    store.doctorAppointment.clearDoctor();
  });

  return {
    doctor: store.doctorAppointment.doctor,
  };
}

export function useAppointmentIntervals(): AppointmentIntervalsComposition {
  const store = useSimpleStore();
  const route = useRoute();
  const checkUserProfile = useCheckUserProfile();

  const hasAppointmentIntervals = computed(
    () => store.doctorAppointment.hasAppointmentIntervals
  );
  const appointmentInterval = computed(
    () => store.doctorAppointment.appointmentInterval
  );

  const loadingIntervalId = ref('');

  function getAppointmentIntervals(): Promise<void> {
    return store.doctorAppointment.getAppointmentIntervals({
      Date: moment().toISOString(true),
      DoctorId: route.params.doctorId as string,
    });
  }

  async function chooseIntervalOrUpdateIntervals(intervalId: string): Promise<void> {
    loadingIntervalId.value = intervalId;

    await store.doctorAppointment.getAppointmentInterval({ intervalId });

    loadingIntervalId.value = '';

    if (appointmentInterval.value.error) {
      notifyError({
        message: 'Не удалось получить информацию о выбранном времени записи',
      });
    } else if (appointmentInterval.value.data?.isFree) {
      checkUserProfile.skipProfileUpdateIfUpdated();
    } else {
      await getAppointmentIntervals();

      notifyError({
        message: 'Выбранное время записи уже занято',
      });
    }
  }

  onMounted(() => {
    getAppointmentIntervals();
  });

  onUnmounted(() => {
    store.doctorAppointment.clearAppointmentIntervals();
  });

  function getIntervalByDay(requestedDate: MomentInput): DoctorIntervalDto | undefined {
    return store.doctorAppointment.appointmentIntervals.data.find(({ date }) => (
      moment(date).isSame(requestedDate, 'day')
    ));
  }

  return {
    appointmentInterval: store.doctorAppointment.appointmentInterval,
    appointmentIntervals: store.doctorAppointment.appointmentIntervals,
    chooseIntervalOrUpdateIntervals,
    getIntervalByDay,
    hasAppointmentIntervals,
    loadingIntervalId,
  };
}

export function useDoctorExperience(): DoctorExperienceComposition {
  const store = useSimpleStore();
  const route = useRoute();

  const experienceYearsAsc = computed(
    () => store.doctorExperience.experienceYearsAsc
  );

  onMounted(() => {
    store.doctorExperience.getDoctorExperience({
      doctorId: route.params.doctorId as string,
    });
  });

  onUnmounted(() => {
    store.doctorExperience.clear();
  });

  return {
    doctorExperience: store.doctorExperience.state,
    experienceYearsAsc,
  };
}
