import {
  State, Mutation, Action, Getter,
} from 'vuex-simple';

import {
  DoctorAppointmentModel, DoctorState, AppointmentIntervalsState, AppointmentIntervalState,
} from './types';
import { DoctorDetailedDto, DoctorIntervalDto, IntervalDto } from '@/types/models';
import { AppointmentIntervalParams, AppointmentIntervalsParams, DoctorParams } from '@/services';

import DoctorAppointmentServiceInstance from '@/services/doctors';

class DoctorAppointmentStore implements DoctorAppointmentModel {
  @State()
  doctor: DoctorState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @State()
  appointmentIntervals: AppointmentIntervalsState = {
    data: [],
    error: '',
    isLoading: false,
  }

  @State()
  appointmentInterval: AppointmentIntervalState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @Getter()
  get hasAppointmentIntervals(): boolean {
    return !!this.appointmentIntervals.data.length;
  }

  @Mutation()
  setDoctorIsLoading(isLoading: boolean): void {
    this.doctor.isLoading = isLoading;
  }

  @Mutation()
  setDoctorError(error: string): void {
    this.doctor.error = error;
  }

  @Mutation()
  setDoctorData(data: DoctorDetailedDto | null): void {
    this.doctor.data = data;
  }

  @Mutation()
  setAppointmentIntervalsIsLoading(isLoading: boolean): void {
    this.appointmentIntervals.isLoading = isLoading;
  }

  @Mutation()
  setAppointmentIntervalsError(error: string): void {
    this.appointmentIntervals.error = error;
  }

  @Mutation()
  setAppointmentIntervalsData(data: DoctorIntervalDto[]): void {
    this.appointmentIntervals.data = data;
  }

  @Mutation()
  setAppointmentIntervalIsLoading(isLoading: boolean): void {
    this.appointmentInterval.isLoading = isLoading;
  }

  @Mutation()
  setAppointmentIntervalError(error: string): void {
    this.appointmentInterval.error = error;
  }

  @Mutation()
  setAppointmentIntervalData(data: IntervalDto | null): void {
    this.appointmentInterval.data = data;
  }

  @Action()
  getDoctor = async (params: DoctorParams): Promise<void> => {
    this.setDoctorIsLoading(true);
    this.setDoctorError('');
    try {
      const doctor = await DoctorAppointmentServiceInstance.getDoctor({ params });

      this.setDoctorData(doctor);
    } catch (error: any) {
      this.setDoctorError(error.massage);
    } finally {
      this.setDoctorIsLoading(false);
    }
  }

  @Action()
  getAppointmentIntervals = async (params: AppointmentIntervalsParams): Promise<void> => {
    this.setAppointmentIntervalsIsLoading(true);
    this.setAppointmentIntervalsError('');
    try {
      const appointmentIntervals = await DoctorAppointmentServiceInstance.getAppointmentIntervals({
        params,
      });

      this.setAppointmentIntervalsData(appointmentIntervals);
    } catch (error: any) {
      this.setAppointmentIntervalsError(error.message);
    } finally {
      this.setAppointmentIntervalsIsLoading(false);
    }
  }

  @Action()
  getAppointmentInterval = async (params: AppointmentIntervalParams): Promise<void> => {
    this.setAppointmentIntervalIsLoading(true);
    this.setAppointmentIntervalError('');
    try {
      const appointmentInterval = await DoctorAppointmentServiceInstance.getAppointmentInterval({
        params,
      });

      this.setAppointmentIntervalData(appointmentInterval as any);
    } catch (error: any) {
      this.setAppointmentIntervalError(error.message);
    } finally {
      this.setAppointmentIntervalIsLoading(false);
    }
  }

  @Action()
  clearDoctor = async (): Promise<void> => {
    this.setDoctorData(null);
  }

  @Action()
  clearAppointmentIntervals = async (): Promise<void> => {
    this.setAppointmentIntervalsData([]);
  }
}

export default DoctorAppointmentStore;
