
import { Options, Vue } from 'vue-class-component';
import { ModelSync, Prop } from 'vue-property-decorator';

import { RDialogProps } from './types';

@Options({})
export default class RDialog extends Vue implements RDialogProps {
  @ModelSync('value', 'change') modelValue!: boolean;

  @Prop(Boolean) fullWidth!: boolean;
}
