import { AxiosRequestConfig } from 'axios';
import { CrudService, BaseService } from '@/api';

import {
  UserDataDto, UserDataResponse, NeedToUpdateProfile, UserInfoDto,
} from '@/types/models';
import { UserInfoByDmTokenParams, UserInfoByBuduTokenParams } from './types';

export class UserService<T, D> extends CrudService<T, D> {
  constructor() {
    super('/User');
  }

  public async updateUser(data: D, config?: AxiosRequestConfig<D>): Promise<T> {
    return this.update(data, config) as Promise<T>;
  }

  public async getNeedToUpdateProfile(): Promise<NeedToUpdateProfile> {
    const { data } = await BaseService.axios.get(`${this.path}/isFilled`);

    return data.needToUpdateProfile;
  }

  public async getUserInfoByDmToken(params: UserInfoByDmTokenParams): Promise<UserInfoDto> {
    const { data } = await BaseService.axios.get(`${this.path}/getByDetMirToken/${params.detMirToken}`);

    return data.data;
  }

  public async getUserInfoByBuduToken(params: UserInfoByBuduTokenParams): Promise<UserInfoDto> {
    const { data } = await BaseService.axios.get(`${this.path}/getByBuduToken/${params.buduToken}`);

    return data.data;
  }
}

export default new UserService<UserDataResponse, UserDataDto>();
