import { PollInterface } from './types';

import { hasTimeout, delayPromise } from '@/utils/requests/async';

export async function poll<Response>(pollingInfo: PollInterface<Response>): Promise<Response | never> {
  const {
    requestCb,
    predicate,
    errorHandler = (): never => {
      throw new Error('polling timeout error');
    },
    interval = 100,
    timeout = 0,
  } = pollingInfo;

  const requestDateStart = Date.now();

  async function run(): Promise<Response | never> {
    try {
      const response = await requestCb();

      if (predicate(response)) {
        return response;
      }

      if (hasTimeout(requestDateStart, timeout)) {
        return errorHandler();
      }

      return delayPromise(interval).then(run);
    } catch (error) {
      if (hasTimeout(requestDateStart, timeout)) {
        return errorHandler();
      }

      // request again if previous request error
      return delayPromise(interval).then(run);
    }
  }

  return run();
}
