import {
  State, Mutation, Action, Getter,
} from 'vuex-simple';
import moment from 'moment';

import {
  DoctorExperienceModel, DoctorExperienceState,
} from './types';
import { DoctorExperienceAndEducationDto, DoctorExperienceDto } from '@/types/models';
import { DoctorExperienceParams } from '@/services';

import DoctorServiceInstance from '@/services/doctors';

class DoctorExperienceStore implements DoctorExperienceModel {
  @State()
  state: DoctorExperienceState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @Getter()
  get experienceYearsAsc(): DoctorExperienceDto[] {
    const experiences = [ ...(this.state.data?.experiences ?? []) ];

    return experiences
      .sort((experience1, experience2) => moment(experience1.dateStart).year() - moment(experience2.dateStart).year());
  }

  @Mutation()
  setDoctorExperienceIsLoading(isLoading: boolean): void {
    this.state.isLoading = isLoading;
  }

  @Mutation()
  setDoctorExperienceError(error: string): void {
    this.state.error = error;
  }

  @Mutation()
  setDoctorExperienceData(data: DoctorExperienceAndEducationDto | null): void {
    this.state.data = data;
  }

  @Action()
  getDoctorExperience = async (params: DoctorExperienceParams): Promise<void> => {
    this.setDoctorExperienceIsLoading(true);
    this.setDoctorExperienceError('');
    try {
      const doctor = await DoctorServiceInstance.getDoctorExperience({ params });

      this.setDoctorExperienceData(doctor);
    } catch (error: any) {
      this.setDoctorExperienceError(error.massage);
    } finally {
      this.setDoctorExperienceIsLoading(false);
    }
  }

  @Action()
  clear = async (): Promise<void> => {
    this.setDoctorExperienceData(null);
  }
}

export default DoctorExperienceStore;
