import {
  State, Mutation, Action, Getter,
} from 'vuex-simple';
import jwtDecode from 'jwt-decode';
import moment, { Moment } from 'moment';

import { Store } from '@/store';

import {
  getBuduTokensFromBrowserStorage,
  getBuduTokensFromQueryParams,
  isTokenValid,
  removeTokensFromBrowserStorage,
  setTokensToBrowserStorage,
} from '@/utils';

import { AuthenticationModel, AuthenticationState } from './types';
import { AuthByRefreshTokenRequest } from '@/services/authentication/types';
import {
  AuthDeviceInfoDto, AuthResponse, DecodedJwtToken, UnwrappedErrorResponse,
} from '@/types/models';

import AuthenticationServiceInstance from '@/services/authentication';

class AuthenticationStore implements AuthenticationModel {
  // eslint-disable-next-line no-useless-constructor
  constructor(private root: Store) {}

  @State()
  state: AuthenticationState = {
    data: null,
    error: null,
    isLoading: false,
  }

  @State()
  smsConfirmUpdateDate: Moment | null = null;

  @Getter()
  get isAuthenticated(): boolean {
    return isTokenValid(this.state.data?.accessToken, this.state.data?.accessExpireTime);
  }

  @Getter()
  get isTokenRecentlyUpdated(): boolean {
    const recentTokenUpdateMs = 60000;

    return !!this.smsConfirmUpdateDate
      && moment().diff(this.smsConfirmUpdateDate) < recentTokenUpdateMs;
  }

  @Getter()
  get decodedToken(): DecodedJwtToken | null {
    return this.state.data?.accessToken
      ? jwtDecode<DecodedJwtToken>(this.state.data.accessToken)
      : null;
  }

  @Mutation()
  setAuthenticationIsLoading(isLoading: boolean): void {
    this.state.isLoading = isLoading;
  }

  @Mutation()
  setAuthenticationError(error: UnwrappedErrorResponse | null): void {
    this.state.error = error;
  }

  @Mutation()
  setAuthenticationData(data: AuthResponse | null): void {
    this.state.data = data;
  }

  @Mutation()
  setSmsConfirmUpdateDate(smsConfirmUpdateDate: Moment | null): void {
    this.smsConfirmUpdateDate = smsConfirmUpdateDate;
  }

  @Action()
  loadGuestTokens = async (): Promise<void> => {
    try {
      this.tryLoadTokensBrowserStorage();
    } catch {
      await this.loginAsGuest(
        new AuthDeviceInfoDto('f8c72ea5-1f78-4c8e-b999-77eb813e3722')
      );
    }
  }

  @Action()
  loadUserTokens = async (): Promise<void> => {
    try {
      this.tryLoadTokensBrowserStorage();
    } catch {
      await this.tryLoadOrRefreshUserTokens();
    }
  }

  @Action()
  tryLoadTokensBrowserStorage = (): void | never => {
    const tokens = getBuduTokensFromBrowserStorage();

    const {
      accessExpireTime, accessToken,
    } = tokens ?? {} as AuthResponse;

    if (isTokenValid(accessToken, accessExpireTime)) {
      this.saveTokensToStoreAndBrowserStorage(tokens as AuthResponse);
    } else {
      throw new Error('No tokens/valid tokens in browser storage');
    }
  }

  @Action()
  tryLoadOrRefreshUserTokens = async (): Promise<void> => {
    const tokens = getBuduTokensFromQueryParams();

    const {
      accessExpireTime, accessToken, refreshExpireTime, refreshToken,
    } = tokens ?? this.state.data ?? {} as AuthResponse;

    // if accessToken valid, then set it
    if (isTokenValid(accessToken, accessExpireTime)) {
      this.saveTokensToStoreAndBrowserStorage(tokens as AuthResponse);
      return;
    }

    // if accessToken not valid, try to refresh it or reset token
    if (isTokenValid(refreshToken, refreshExpireTime)) {
      await this.refreshToken(
        new AuthByRefreshTokenRequest(refreshToken)
      );
    }
  }

  @Action()
  loginAsGuest = async (deviceInfo: AuthDeviceInfoDto): Promise<void> => {
    this.setAuthenticationIsLoading(true);
    this.setAuthenticationError(null);
    this.root.modals.updateServerErrorModalShow({
      show: false,
    });
    try {
      const tokens = await AuthenticationServiceInstance.loginAsGuest(deviceInfo);

      this.saveTokensToStoreAndBrowserStorage(tokens);
    } catch (error: any) {
      this.setAuthenticationError(error.message);
      this.root.modals.updateServerErrorModalShow({
        show: true,
      });
    } finally {
      this.setAuthenticationIsLoading(false);
    }
  }

  @Action()
  refreshToken = async (refreshToken: AuthByRefreshTokenRequest): Promise<void> => {
    this.setAuthenticationIsLoading(true);
    this.setAuthenticationError(null);
    try {
      const refreshedTokens = await AuthenticationServiceInstance.refreshToken(refreshToken);

      this.saveTokensToStoreAndBrowserStorage(refreshedTokens);
    } catch {
      this.setAuthenticationError(null);
      this.clear();
    } finally {
      this.setAuthenticationIsLoading(false);
    }
  }

  @Action()
  saveTokensToStoreAndBrowserStorage = (tokens: AuthResponse): void => {
    this.setAuthenticationData(tokens);
    setTokensToBrowserStorage(tokens);
  }

  @Action()
  clear = (): void => {
    this.setAuthenticationData(null);
    removeTokensFromBrowserStorage();
  }
}

export default AuthenticationStore;
