
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { RSeparatorProps } from './types';

@Options({})
export default class RSeparator extends Vue implements RSeparatorProps {
  @Prop({ type: [ Boolean, String ], default: true }) readonly spaced!: boolean | string | undefined;

  @Prop({ type: String, default: '' }) readonly color!: string;
}
