
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

import { QSkeletonType, RSkeletonProps } from './types';

@Options({})
export default class RSkeleton extends Vue implements RSkeletonProps {
  @Prop({ type: String, default: 'rect' }) readonly type!: QSkeletonType;

  @Prop({ type: String, default: 'div' }) readonly tag!: QSkeletonType;

  @Prop(String) readonly height!: string;

  @Prop(Boolean) readonly square!: boolean;

  @Prop(String) readonly width!: string;
}
