import axios from 'axios';
import { Directive } from 'vue';

export const AuthSrcDirective: Directive = {
  beforeMount: async (element, binding) => {
    if (binding.oldValue === undefined || binding.value !== binding.oldValue) {
      const imageUrl = binding.value;

      try {
        const { data } = await axios.get(imageUrl, { responseType: 'blob' });
        const url = URL.createObjectURL(data);

        // eslint-disable-next-line no-param-reassign
        element.querySelector('img').src = url;

        // eslint-disable-next-line no-empty
      } catch {}
    }
  },
  beforeUnmount: element => {
    URL.revokeObjectURL(element.querySelector('img').src);
  },
};
