import { BrowserStorageKeys } from '@/types/common';
import { ThemeTypes } from '@/types/common/theming';

import { defaultTheme, themes } from '@/common';

export function isValidTheme(searchValue: string | null): searchValue is ThemeTypes {
  if (!searchValue) {
    return false;
  }

  return themes.includes(searchValue as ThemeTypes);
}

export function getThemeFromSearch(): ThemeTypes | null {
  const params = new URLSearchParams(window.location.search);
  const theme = params.get('theme');

  if (!isValidTheme(theme)) {
    return null;
  }

  return theme;
}

export function setThemeToBrowserStorage(theme: ThemeTypes): void {
  window.sessionStorage.setItem(BrowserStorageKeys.theme, theme);
}

export function getThemeFromBrowserStorage(): ThemeTypes | null {
  const theme = window.sessionStorage.getItem(BrowserStorageKeys.theme);

  if (!isValidTheme(theme)) {
    return null;
  }

  return theme;
}

export function getThemeFromStorageOrDefault(): ThemeTypes {
  return getThemeFromBrowserStorage() ?? defaultTheme;
}
