import { AxiosRequestConfig } from 'axios';
import { BaseService, CrudService } from '@/api';

import {
  ConfirmationInfoDto, PaymentInfoResponse, ConsultationDto, ConsultationCreatedResponse, OrderDto,
} from '@/types/models';
import {
  ConsultationCreatedParams, OrderStatusParams, PaymentInfoRequest, СonsultationOnTheOrderParams,
} from './types';

export class OrdersService<T, D> extends CrudService<T, D> {
  constructor() {
    super('/Orders');
  }

  public async getConfirmationInfo(config?: AxiosRequestConfig<D>): Promise<T> {
    const response = await BaseService.axios.get(`${this.path}/confirmationInfo`, config);

    return response.data;
  }

  public async createOrderWithPayment(
    data: PaymentInfoRequest,
    config?: AxiosRequestConfig<PaymentInfoRequest>
  ): Promise<PaymentInfoResponse> {
    const response = await BaseService.axios.post(this.path, data, config);

    return response.data;
  }

  public async getConsultation(config?: AxiosRequestConfig<СonsultationOnTheOrderParams>): Promise<ConsultationDto> {
    const response = await BaseService.axios.get(`${this.path}/${config?.params?.orderId}/consultation`);

    return response.data;
  }

  public async checkConsultationCreated(config?: AxiosRequestConfig<ConsultationCreatedParams>): Promise<ConsultationCreatedResponse> {
    const response = await BaseService.axios.get(`${this.path}/${config?.params?.orderId}/isConsultationCreated`);

    return response.data;
  }

  public async getOrderStatus(config?: AxiosRequestConfig<OrderStatusParams>): Promise<OrderDto> {
    const response = await BaseService.axios.get(`${this.path}/${config?.params?.orderId}`);

    return response.data;
  }
}

export default new OrdersService<ConfirmationInfoDto, unknown>();
