import {
  State, Mutation, Action, Getter,
} from 'vuex-simple';

import { Store } from '@/store';

import { ConsultationModel, ConsultationState, ConsultationCancelState } from './types';
import { ConsultationDto, DocumentDto } from '@/types/models';
import { ConsultationCancelParams, ConsultationViewParams } from '@/services';
import { consultationStatusesMap } from '@/common';
import { СonsultationOnTheOrderParams } from '@/services/orders/types';

import ConsultationServiceInstance from '@/services/consultation';
import OrdersServiceInstance from '@/services/orders';

class ConsultationStore implements ConsultationModel {
  // eslint-disable-next-line no-useless-constructor
  constructor(private root: Store) {}

  @State()
  state: ConsultationState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @State()
  consultationCancel: ConsultationCancelState = {
    data: null,
    error: '',
    isLoading: false,
  }

  @Getter()
  get mappedStatus(): string {
    if (!this.state.data?.status) {
      return '';
    }

    return consultationStatusesMap[this.state.data.status];
  }

  @Getter()
  get conclusionFile(): DocumentDto | undefined {
    return this.state.data?.documents.find(({ fileId }) => (
      fileId === this.state.data?.conclusionId
    ));
  }

  @Mutation()
  setConsultationIsLoading(isLoading: boolean): void {
    this.state.isLoading = isLoading;
  }

  @Mutation()
  setConsultationError(error: string): void {
    this.state.error = error;
  }

  @Mutation()
  setConsultationData(data: ConsultationDto | null): void {
    this.state.data = data;
  }

  @Mutation()
  setConsultationCancelIsLoading(isLoading: boolean): void {
    this.consultationCancel.isLoading = isLoading;
  }

  @Mutation()
  setConsultationCancelError(error: string): void {
    this.consultationCancel.error = error;
  }

  @Mutation()
  setConsultationCancelData(data: ConsultationDto | null): void {
    this.consultationCancel.data = data;
  }

  @Action()
  getConsultation = async (params: ConsultationViewParams): Promise<void> => {
    this.setConsultationIsLoading(true);
    this.setConsultationError('');
    this.root.modals.updateServerErrorModalShow({
      show: false,
    });
    try {
      const specialities = await ConsultationServiceInstance.getConsultation({ params });

      this.setConsultationData(specialities);
    } catch (error: any) {
      this.setConsultationError(error.message);
      this.root.modals.updateServerErrorModalShow({
        show: true,
      });
    } finally {
      this.setConsultationIsLoading(false);
    }
  }

  @Action()
  getConsultationByOrderId = async (params: СonsultationOnTheOrderParams): Promise<void> => {
    this.setConsultationIsLoading(true);
    this.setConsultationError('');
    this.root.modals.updateServerErrorModalShow({
      show: false,
    });
    try {
      const consultation = await OrdersServiceInstance.getConsultation({ params });

      this.setConsultationData(consultation);
    } catch (error: any) {
      this.setConsultationError(error.message);
      this.root.modals.updateServerErrorModalShow({
        show: true,
      });
    } finally {
      this.setConsultationIsLoading(false);
    }
  }

  @Action()
  cancelConsultation = async (params: ConsultationCancelParams): Promise<void> => {
    this.setConsultationCancelIsLoading(true);
    this.setConsultationCancelError('');
    try {
      const cancelledConsultation = await ConsultationServiceInstance.cancelConsultation({ params });

      this.setConsultationCancelData(cancelledConsultation);
    } catch (error: any) {
      this.setConsultationCancelError(error.message);
    } finally {
      this.setConsultationCancelIsLoading(false);
    }
  }

  @Action()
  clear = async (): Promise<void> => {
    this.setConsultationData(null);
  }

  @Action()
  clearConsultationCancel = async (): Promise<void> => {
    this.setConsultationCancelData(null);
  }
}

export default ConsultationStore;
