import { ref, onBeforeMount } from 'vue';
import { useRoute, useRouter } from 'vue-router';

import { RoutePaths } from '@/router/types';
import { RedirectComposition } from './types';

export function useRedirect(): RedirectComposition {
  const route = useRoute();
  const router = useRouter();

  const redirectToPath = ref('');

  function redirect(): void {
    router.push(redirectToPath.value);
  }

  onBeforeMount(() => {
    const { query, redirectedFrom } = route;

    redirectToPath.value = query.redirectTo as string
      || redirectedFrom?.fullPath
      || RoutePaths.NOT_FOUND;
  });

  return {
    redirectToPath,
    redirect,
  };
}
