import intersection from 'lodash/intersection';

import { NavigationGuard } from './types';
import { Permissions } from '@/types/common';
import { RouteNames } from '@/router/types';
import { Store } from '@/store';

export default abstract class AbstractGuard {
  abstract store: Store;

  abstract guard: NavigationGuard;

  get isAuthenticated(): boolean {
    return !!this.store?.authentication.isAuthenticated;
  }

  hasPermissions(permissions: Permissions[]): boolean {
    const routePermissions = (this.guard.to.meta.permissions as Permissions[]);

    return !!intersection(routePermissions, permissions).length;
  }

  abstract nextName(): Promise<RouteNames>;
}
